@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.order-receiver {

  .c-text--caution {

    p {
      margin-top: 0;
    }
  }

  .registered-address {
    border-bottom: 1px solid #e4e4e4;
    .c-selectbox-form__wrap{
      margin-right: 15px;
      @include media(sm) {
        margin-right: 0;
      }
    }

    .c-btn--address{
      @include media(sm) {
        display: flex;
      }
    }

    .prefecture-txt {
      @include media(sm) {
        align-items: center;
        display: flex;
        justify-content: space-between;
      }

      &:before {
        @include media(sm) {
          left: 140px !important;
        }

        @include media(se) {
          left: 120px !important;
        }
      }

      select {
        margin-right: 20px !important;

        @include media(sm) {
          width: 140px;
        }

        @include media(se) {
          width: 120px;
        }
      }
    }
  }

  .registered-search {
    align-items: center;
    display: flex;
  }
}
