@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.l-head {
  &__pattern1{

    .l-breadcrumbs{
      margin-top: 35px;
      @include media(sm) {
        margin-top: vwset(28);
      }
    }

    &__content{
      margin-top: 60px;

      @include media(sm) {
        margin-top: vwset(40);
      }
    }

    &__content-main{
      margin-top: 60px;
      @include media(sm){
         margin-top: vwset(19);
      }
    }

  }

  &__pattern2{
    .l-container {
      margin-top: 50px;

      @include media(sm) {
        margin-top: vwset(40);
      }
    }

    .c-step{
      margin-top: 35px;
      @include media(sm) {
        margin-top: vwset(28);
      }
    }

    &__content{
       margin-top: 50px;
      @include media(sm) {
        margin-top: vwset(30);
      }
    }


  }

}
