@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.search {
  .l-head__pattern1__content{
    margin-top: 40px;
    @include media(sm) {
      margin-top: vwset(40);
    }
  }


  .c-title--page {
    @include media(sm) {
      display: block;
      position: relative;

      strong,
      small {
        display: block;
        margin: 0;
      }

      button {
        align-items: center;
        border: solid 2px #4aa2dc;
        border-radius: 3px;
        color: #4aa2dc;
        display: flex;
        height: vwset(47);
        padding: vwset(10);
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: vwset(110);

        @include fz(14, 19, 600);

        svg {
          margin-right: vwset(5);
          width: vwset(17);
        }
      }
    }
  }

  // 

  &__header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;

    &--no-margin {
      margin-top: 0;
    }

    &--sort {
      align-items: center;
      display: flex;

      .c-selectbox {

        &:nth-of-type(1) {
          width: 120px;
        }

        &:nth-of-type(n + 2) {
          margin-left: 10px;
          width: 80px;
        }
      }
    }

    &--pagecount {
      @include fz(14, 19, 600);
    }

    @include media(sm) {
      display: block;
      margin-top: vwset(30);

      &--sort {

        .c-selectbox {

          &:nth-of-type(1) {
            width: auto;
          }

          &:nth-of-type(n + 2) {
            margin-left: vwset(10);
            width: auto;
          }
        }
      }

      &--pagecount {
        @include fz(13, 16, 600);

        margin-top: vwset(14);
      }
    }
  }

  // 

  &__list {
    margin-top: 20px;

    @include media(sm) {
      margin-top: vwset(30);
    }
  }
}
