
@charset "utf-8";

//mixinvariables
@use "sass:math";

// break point
$breakPoint: (
  hlg: 1500px,
  lg: 900px,
  sm: 899.9px,
  se: 360px
);

// color
// ----------------------------------------------------------
$color: (
  white: #fff,
  whitebg:#f8f8f8,
  gold: #e0bf09,
  blonse: #9f5a16,
  black: #393939,
  gray: #a0a0a0,
  linegray: #e4e4e4,
  orange: #e07409,
  red: #e31616,
  green: #51ba23,
  blue: #2369ba,
  logoblue: #4aa2db,
  hoverblue: #3669b4
);

// 03__
// ----------------------------------------------------------

@mixin fz($size, $height: false, $weight: false) {
  //font-size: rem($size);
  font-size: #{math.div($size, 10)}rem;

  @if $weight {
    font-weight: $weight;
  }

  @if $height {
    line-height: math.div($height, $size);
  }
}

// 04__letter-spacing
// ----------------------------------------------------------

@mixin letterSpacing($tracking) {
  letter-spacing: #{math.div($tracking, 1000)}em;
}


// 06__media query
// ----------------------------------------------------------

@mixin media($size, $px: false) {

  // sm only
  @if ($size == "sm") {
    @media screen and (max-width: map-get($breakPoint, sm)) {
      @content;
    }
  }

  // se only
  @if ($size == "se") {
    @media screen and (max-width: map-get($breakPoint, se)) {
      @content;
    }
  }

  // between lg and $mainW
  @else if ($size == "mid") {
    @media screen and (min-width: map-get($breakPoint, lg)) and (max-width: $mainW - 1), print {
      @content;
    }
  }

  // lg only
  @else if ($size == "lg") {
    @media screen and (min-width: map-get($breakPoint, lg)), print {
      @content;
    }
  }

  // hlg only
  @else if ($size == "hlg") {
    @media screen and (min-width: map-get($breakPoint, hlg)), print {
      @content;
    }
  }

  // retina only
  @else if ($size == "retina") {
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
      @content;
    }
  }

  @if ($px) {
    @if ($size == "max-width") {
      @media screen and (max-width: $px) {
        @content;
      }
    }

    @else if ($size == "min-width") {
      @media screen and (min-width: $px) {
        @content;
      }
    }

    @else if ($size == "max-height") {
      @media screen and (max-height: $px) {
        @content;
      }
    }

    @else if ($size == "min-height") {
      @media screen and (min-height: $px) {
        @content;
      }
    }
  }
}

// 41__HITMALL  
// ----------------------------------------------------------

@mixin text($size) {

  @if ($size == "xl") {
    @include fz(24, 41, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(22, 41, 600);
      @include letterSpacing(50);
    }
  }

    @else if ($size == "l") {
      @include fz(20, 34, 600);
      @include letterSpacing(50);
      @include media(sm) {
        @include fz(18, 34, 600);
        @include letterSpacing(50);
    }
  }

  @else if ($size == "m") {
    @include fz(16, 27, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(14, 27, 600);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "s") {
    @include fz(14, 24, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(12, 27, 600);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "xs") {
    @include fz(13, 23);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(11, 23);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "xxs") {
      @include fz(12, 18);
      @include letterSpacing(50);
      @include media(sm) {
        @include fz(10, 23);
        @include letterSpacing(50);
      }
  }

  // xxs bold
  @else if ($size == "xxs-w6") {
    @include fz(12, 18, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(10, 23, 600);
      @include letterSpacing(50);
    }
  }

}

%alert {
  @include text(s);
  margin: 10px auto;
  padding: 7px 10px 5px;
  border: solid 1px;
  border-radius: 3px;
  list-style: none;
}

.c-alert {
  &--error {
    @extend %alert;
    background-color: lighten(map-get($color, red),47%);
    border-color: map-get($color, red);
    color: map-get($color, red);
  }
  &--info {
    @extend %alert;
    background-color: lighten(map-get($color, blue),47%);
    border-color: map-get($color, blue);
    color: map-get($color, black);
  }
  &--warning {
    @extend %alert;
    background-color: lighten(map-get($color, red),47%);
    border-color: map-get($color, red);
    color: map-get($color, red);
  }
  &--danger {
    @extend %alert;
    background-color: lighten(map-get($color, orange),47%);
    border-color: map-get($color, orange);
    color: map-get($color, black);
  }

  &__wrap {
    margin-top: -10px;
  }
}
