@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.l-mainwrap {
  opacity: 0;
  position: relative;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);

  .is-loaded & {
    opacity: 1;
    visibility: visible;
  }
}

.l-page {
  margin-bottom: 120px;
  padding-top: 90px;
  position: relative;

  @include media(sm) {
    margin-bottom: vwset(60);
    padding-top: vwset(70);
  }

  .noheader & {
    padding: 60px 0;

    @include media(sm) {
      padding: vwset(30) 0;
    }
  }
}

.l-container {
  display: flex;
  justify-content: space-between;

  @include width(1200px, 20px, 0);

  @include media(sm) {
    margin-top: vwset(40);
  }

  // 2  
  &--title-under-breadcrumb {
    display: block;
    @include width(1200px, 20px, 0);
    @include media(sm) {
      margin: vwset(40) vwset(12) 0;
      width: calc(100% - #{vwset(24)});
    }
  }

    // 2  
    &--title-no-breadcrumb {
      display: block;
      @include width(1200px, 20px, 0);
      @include media(sm) {
        margin: 0 vwset(12) 0;
        width: calc(100% - #{vwset(24)});
      }
    }

}

.l-main {
  //width: calc(100% - 190px - 50px);
  //@include width(960px, 0, vwset(12));
  width: 960px;

  &.full {
    width: 100%;

    @include media(sm) {
      width: calc(100% - #{vwset(24)});
    }
  }

  @include media(sm) {
    margin: 0 vwset(12);
    width: calc(100% - #{vwset(24)});
  }
}

//section 

.l-section {
  margin-top: 100px;
  @include media(sm) {
    margin-top: vwset(60);
  }
  &--small {
    margin-top: 80px;
    @include media(sm) {
      margin-top: vwset(64);
    }
  }
  &--xs {
    margin-top: 60px;
    @include media(sm) {
      margin-top: vwset(48);
    }
  }
  &--top {
    margin-top: 50px;
    @include media(sm) {
      margin-top: vwset(50);
    }
  }
}

//PC
.l-lg {
  @include media(sm) {
    display: none !important;
  }
}

//SP
.l-sm {
  @include media(lg) {
    display: none !important;
  }
}
