// 01__
// ----------------------------------------------------------

@mixin hideText() {
  color: transparent;
  font: 0/0;
  overflow: hidden;
  text-shadow: none;
}

// 02__
// ----------------------------------------------------------

@mixin ellipsis($is-clamp: false) {
  @if ( $is-clamp ) {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    word-break: break-all;
  }

  @else {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

@mixin notEllipsis() {
  overflow: inherit;
  text-overflow: inherit;
  white-space: inherit;
}

// 03__
// ----------------------------------------------------------

@mixin fz($size, $height: false, $weight: false) {
  //font-size: rem($size);
  font-size: #{math.div($size, 10)}rem;

  @if $weight {
    font-weight: $weight;
  }

  @if $height {
    line-height: math.div($height, $size);
  }
}

@mixin fzs($size, $height: false, $weight: false) {
  $size: $size/2;

  //font-size: rem($size);
  font-size: #{math.div($size, 10)}rem;

  @if $weight {
    font-weight: $weight;
  }

  @if $height {
    line-height: math.div(math.div($height, 2), $size);
  }
}

// 04__letter-spacing
// ----------------------------------------------------------

@mixin letterSpacing($tracking) {
  letter-spacing: #{math.div($tracking, 1000)}em;
}

// 05__gradation
// ----------------------------------------------------------

@mixin gradation($topColor, $bottomColor, $angle: to bottom) {
  background: mix($topColor, $bottomColor, 50%); //
  background: linear-gradient($angle, $topColor, $bottomColor);
}

// 06__media query
// ----------------------------------------------------------

@mixin media($size, $px: false) {

  // sm only
  @if ($size == "sm") {
    @media screen and (max-width: map-get($breakPoint, sm)) {
      @content;
    }
  }

  // se only
  @if ($size == "se") {
    @media screen and (max-width: map-get($breakPoint, se)) {
      @content;
    }
  }

  // between lg and $mainW
  @else if ($size == "mid") {
    @media screen and (min-width: map-get($breakPoint, lg)) and (max-width: $mainW - 1), print {
      @content;
    }
  }

  // lg only
  @else if ($size == "lg") {
    @media screen and (min-width: map-get($breakPoint, lg)), print {
      @content;
    }
  }

  // hlg only
  @else if ($size == "hlg") {
    @media screen and (min-width: map-get($breakPoint, hlg)), print {
      @content;
    }
  }

  // retina only
  @else if ($size == "retina") {
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
      @content;
    }
  }

  @if ($px) {
    @if ($size == "max-width") {
      @media screen and (max-width: $px) {
        @content;
      }
    }

    @else if ($size == "min-width") {
      @media screen and (min-width: $px) {
        @content;
      }
    }

    @else if ($size == "max-height") {
      @media screen and (max-height: $px) {
        @content;
      }
    }

    @else if ($size == "min-height") {
      @media screen and (min-height: $px) {
        @content;
      }
    }
  }
}

// 07__
// ----------------------------------------------------------

@mixin arrow($width, $bold, $color, $deg) {
  @include icon-ini;

  border-color: $color;
  border-style: solid;
  border-width: 0 #{$bold}px #{$bold}px 0;
  height: #{$width}px;
  vertical-align: middle;
  width: #{$width}px;
  //
  @if ($deg == "left") {
    transform: rotate(135deg);
  }

  @else if ($deg == "top") {
    transform: rotate(225deg);
  }

  @else if ($deg == "right") {
    transform: rotate(-45deg);
  }

  @else if ($deg == "bottom") {
    transform: rotate(45deg);
  }

  @include ie11() {
    margin-bottom: 1px !important;
  }
}

// 08__
// ----------------------------------------------------------

@mixin balloonArrow(
  $width: 10px,
  $height: 5px,
  $color: #000,
  $position: bottom
) {
  position: relative;

  @if ($position == "top") {

    &:after {
      border-color: transparent transparent $color transparent;
      border-width: 0 #{$width/2}px #{$height}px #{$width/2}px;
      left: calc(50% - #{$width/2}px);
      top: -#{$height}px;
    }
  }

  @else if ($position == "right") {

    &:after {
      border-color: transparent transparent transparent $color;
      border-width: #{$width/2}px 0 #{$width/2}px #{$height}px;
      right: -#{$height}px;
      top: calc(50% - #{$width/2}px);
    }
  }

  @else if ($position == "bottom") {

    &:after {
      border-color: $color transparent transparent transparent;
      border-width: #{$height}px #{$width/2}px 0 #{$width/2}px;
      bottom: -#{$height}px;
      left: calc(50% - #{$width/2}px);
    }
  }

  @else if ($position == "left") {

    &:after {
      border-color: transparent $color transparent transparent;
      border-width: #{$width/2}px #{$height}px #{$width/2}px 0;
      left: -#{$height}px;
      top: calc(50% - #{$width/2}px);
    }
  }

  &:after {
    border-style: solid;
    content: "";
    display: inline-block;
    height: 0;
    position: absolute;
    width: 0;
  }
}

// 09__
// ----------------------------------------------------------

@mixin icon-ini {
  content: "";
  display: inline-block;
}

// 10__
// ----------------------------------------------------------

@mixin hoverOpacity($opacity, $ms: false, $ease: false) {
  @include media(lg) {
    @if $ease {
      transition: opacity $ms $ease;
    }

    @else {
      transition: opacity $comMS $comES;
    }

    &:hover {
      opacity: $opacity;
    }
  }
}

// 11__object-fit iePolyfill
// ----------------------------------------------------------

@mixin object-fit($fit: fill, $position: null) {
  -o-object-fit: $fit;
  object-fit: $fit;

  @if $position {
    -o-object-position: $position;
    object-position: $position;
    /* stylelint-disable-next-line */
    font-family: "object-fit: #{$fit}; object-position: #{$position}";
  }

  @else {
    font-family: "object-fit: #{$fit}";
  }
}

// 12__form
// ----------------------------------------------------------

@mixin formReset() {

  input,
  button,
  textarea,
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: none;
    border: none;
    border-radius: 0;
    margin: 0;
    padding: 0;
  }

  select::-ms-expand {
    display: none;
  }
}

// 13__ie11 (lg / sm)
// ----------------------------------------------------------

@mixin ie11($size: false) {

  html[data-browser = "ie"][data-browserversion = "11"] & {
    @if ($size) {
      @if ($size == "lg") {
        @media screen and (min-width: map-get($breakPoint, lg)) {
          @content;
        }
      }

      @else if ($size == "sm") {
        @media screen and (max-width: map-get($breakPoint, sm)) {
          @content;
        }
      }
    }

    @else {
      @content;
    }
  }
}

@mixin notie11($size: false) {

  html:not([data-browser = "ie"]):not([data-browserversion = "11"]) & {
    @if ($size) {
      @if ($size == "lg") {
        @media screen and (min-width: map-get($breakPoint, lg)) {
          @content;
        }
      }

      @else if ($size == "sm") {
        @media screen and (max-width: map-get($breakPoint, sm)) {
          @content;
        }
      }
    }

    @else {
      @content;
    }
  }
}

// 14__edge (lg / sm)
// ----------------------------------------------------------

@mixin edge($size: false, $engine: "edgehtml") {
  html[data-browser="edge"][data-browserengine="#{$engine}"] & {
    @if ($size) {
      @if ($size == "lg") {
        @media screen and (min-width: map-get($breakPoint, lg)) {
          @content;
        }
      }

      @else if ($size == "sm") {
        @media screen and (max-width: map-get($breakPoint, sm)) {
          @content;
        }
      }
    }

    @else {
      @content;
    }
  }
}

// 15__firefox (lg / sm)
// ----------------------------------------------------------

@mixin firefox($size: false) {

  html[data-browser = "firefox"] & {
    @if ($size) {
      @if ($size == "lg") {
        @media screen and (min-width: map-get($breakPoint, lg)) {
          @content;
        }
      }

      @else if ($size == "sm") {
        @media screen and (max-width: map-get($breakPoint, sm)) {
          @content;
        }
      }
    }

    @else {
      @content;
    }
