// 16__safari (lg / sm)
// ----------------------------------------------------------

@mixin safari($size: false) {
  @if ($size == "sm") {

    html[data-browser = "mobile-safari"] & {
      @content;
    }
  }

  @else {

    html[data-browser = "safari"] & {
      @content;
    }
  }
}

// 17__tablet (portrait / landscape)
// ----------------------------------------------------------

@mixin tablet($orientation: false) {
  @if ($orientation) {
    html[data-type="tablet"][data-orientation="#{$orientation}"] & {
      @content;
    }
  }

  @else {

    html[data-type = "tablet"] & {
      @content;
    }
  }
}

// 18__ipados (portrait / landscape) 
// ----------------------------------------------------------

@mixin ipados($orientation: false) {
  @if ($orientation) {
    html[data-type="laptop"][data-os="mac-os"][data-browser="safari"][data-touchsupport="true"][data-orientation="#{$orientation}"]
    & {
      @content;
    }
  }

  @else {

    html[data-type = "laptop"][data-os = "mac-os"][data-browser = "safari"][data-touchsupport = "true"] & {
      @content;
    }
  }
}

// 19__first, notfirst
// ----------------------------------------------------------

@mixin first {

  &:first-of-type {
    @content;
  }
}

@mixin notfirst($child: false) {
  @if ($child) {

    &:not(:first-child) {
      @content;
    }
  }

  @else {

    &:not(:first-of-type) {
      @content;
    }
  }
}

// 20__last, notlast
// ----------------------------------------------------------

@mixin last {

  &:last-of-type {
    @content;
  }
}

@mixin notlast {

  &:not(:last-of-type) {
    @content;
  }
}

// 21__expand
// ----------------------------------------------------------

@mixin expand($top, $right, $bottom, $left) {
  margin: #{$top}px #{$right}px #{$bottom}px #{$left}px;
}

// 22__expansion
// ----------------------------------------------------------

@mixin expansion($verticale, $horizontal, $scale) {
  transform: scale($scale);
  transform-origin: $verticale $horizontal;
}

// 23__placeholder
// ----------------------------------------------------------

@mixin placeholder {

  &:placeholder {
    @content;
  }

  &:placeholder-shown {
    @content;
  }

  &::-webkit-input-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }
}

// 24__picture100
// ----------------------------------------------------------

@mixin picture100 {

  picture,
  source,
  img {
    @include object-fit(cover);

    height: 100%;
    width: 100%;
  }
}

// 25__triangle
// ----------------------------------------------------------

@mixin triangle($width, $height, $color, $rotate) {
  border-style: solid;
  content: "";
  height: 0;
  position: absolute;
  width: 0;

  @if ($rotate == "top") {
    border-color: transparent transparent $color transparent;
    border-width: 0 #{$width/2}px #{$height}px #{$width/2}px;
  }

  @else if ($rotate == "right") {
    border-color: transparent transparent transparent $color;
    border-width: #{$width/2}px 0 #{$width/2}px #{$height}px;
  }

  @else if ($rotate == "bottom") {
    border-color: $color transparent transparent transparent;
    border-width: #{$height}px #{$width/2}px 0 #{$width/2}px;
  }

  @else if ($rotate == "left") {
    border-color: transparent $color transparent transparent;
    border-width: #{$width/2}px #{$height}px #{$width/2}px 0;
  }
}

// 26__circle
// ----------------------------------------------------------

@mixin circle($width, $height, $color) {
  background-color: #{$color};
  border-radius: 50%;
  content: "";
  height: #{$height}px;
  position: absolute;
  width: #{$width}px;
}

// 27__square
// ----------------------------------------------------------

@mixin square($width, $height, $color) {
  background-color: #{$color};
  content: "";
  height: #{$height}px;
  position: absolute;
  width: #{$width}px;
}

// 28__rightTriangle
// ----------------------------------------------------------

@mixin rightTriangle($width, $color, $rotate) {
  border-style: solid;
  content: "";
  height: 0;
  position: absolute;
  width: 0;

  @if ($rotate == 1) {
    border-color: $color transparent transparent transparent;
    border-width: #{$width}px #{$width}px 0 0;
  }

  @else if ($rotate == 2) {
    border-color: transparent $color transparent transparent;
    border-width: 0 #{$width}px 0 #{$width}px 0;
  }

  @else if ($rotate == 3) {
    border-color: transparent transparent $color transparent;
    border-width: 0 0 #{$width}px #{$width}px;
  }

  @else if ($rotate == 4) {
    border-color: transparent transparent transparent $color;
    border-width: #{$width}px 0 0 #{$width}px;
  }
}

// 29__isoscelesTriangle
// ----------------------------------------------------------

@mixin isoscelesTriangle($width, $height, $color, $rotate) {
  border-style: solid;
  content: "";
  height: 0;
  position: absolute;
  width: 0;

  @if ($rotate == 1) {
    border-color: $color transparent transparent transparent;
    border-width: #{$width}px #{$width}px 0 0;
  }

  @else if ($rotate == 2) {
    border-color: transparent $color transparent transparent;
    border-width: 0 #{$width}px 0 #{$width}px 0;
  }

  @else if ($rotate == 3) {
    border-color: transparent transparent $color transparent;
    border-width: 0 0 #{$width}px #{$width}px;
  }

  @else if ($rotate == 4) {
    border-color: transparent transparent transparent $color;
    border-width: #{$height}px 0 0 #{$width}px;
  }
}

// 30__100vh
// ----------------------------------------------------------

@mixin vh100($name, $target: height) {
  #{$target}: 100vh;
  #{$target}: calc(var(#{$name}, 1vh) * 100);
}

// 31__orientation
// ----------------------------------------------------------

@mixin orientation($orientation: landscape, $type: mobile) {
  html[data-type="#{$type}"][data-orientation="#{$orientation}"] & {
    @content;
  }
}

// 32__
// ----------------------------------------------------------
@mixin hoverDark($target: false, $pos: after) {
  @include media(lg) {
    @if ($target) {

      &:hover {
        opacity: 1;

        #{$target} {

          &:#{$pos} {
            background-color: rgba(#000, .6);
          }
        }
      }

      #{$target} {
        position: relative;

        &:#{$pos} {
          @include icon-ini;

          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          transition: $comMS $comES;
          width: 100%;
        }
      }
    }

    @else {
      position: relative;

      &:hover {
        opacity: 1;

        &:#{$pos} {
          background-color: rgba(#000, .6);
        }
      }

      &:#{$pos} {
        @include icon-ini;

        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        transition: $comMS $comES;
        width: 100%;
      }
    }
  }
}

