// 36__not macos and ios
// ----------------------------------------------------------

@mixin notMacosIos {

  html:not([data-os = "mac-os"]):not([data-os = "ios"]) & {
    @content;
  }
}

// 37__android
// ----------------------------------------------------------

@mixin android {

  html[data-os = "android"] & {

    @content;
  }
