// 38__
// ----------------------------------------------------------

@mixin width($width: 1000px, $marginLg: 0, $marginSm: 0) {
  margin-left: auto;
  margin-right: auto;
  max-width: $width;

  @if $marginLg {
    width: calc(100% - #{$marginLg} * 2);
  }
  @else {
    width: auto;
  }

  @include media(sm) {
    @if $marginSm {
      width: calc(100% - #{$marginSm} * 2);
    }
    @else {
      width: auto;
    }
  }
}

// 39__
// ----------------------------------------------------------

@mixin placeholder() {
  &:placeholder-shown {
    @content;
  }

  &::-webkit-input-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
    opacity: 1;
  }

  &::-moz-placeholder {
    @content;
    opacity: 1;
  }

  &:-ms-input-placeholder {
    @content;
  }
}

// 40_vw
// ----------------------------------------------------------

@function vwset($num, $baseWidth: $smBasis) {
  @return (math.div($num, $baseWidth) * 100vw);
}


// 41__HITMALL  
// ----------------------------------------------------------

@mixin text($size) {

  @if ($size == "xl") {
    @include fz(24, 41, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(22, 41, 600);
      @include letterSpacing(50);
    }
  }

    @else if ($size == "l") {
      @include fz(20, 34, 600);
      @include letterSpacing(50);
      @include media(sm) {
        @include fz(18, 34, 600);
        @include letterSpacing(50);
    }
  }

  @else if ($size == "m") {
    @include fz(16, 27, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(14, 27, 600);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "s") {
    @include fz(14, 24, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(12, 27, 600);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "xs") {
    @include fz(13, 23);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(11, 23);
      @include letterSpacing(50);
    }
  }

  @else if ($size == "xxs") {
      @include fz(12, 18);
      @include letterSpacing(50);
      @include media(sm) {
        @include fz(10, 23);
        @include letterSpacing(50);
      }
  }

  // xxs bold
  @else if ($size == "xxs-w6") {
    @include fz(12, 18, 600);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(10, 23, 600);
      @include letterSpacing(50);
    }
}

}
