@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.product-detail {

  // 

  &__base {
    align-items: flex-start;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    &--info {
      width: 470px;
    }

    .c-product-list__item--status {
      justify-content: flex-start;
      position: static;
      width: auto;

      span {

        &:nth-of-type(n + 2) {
          margin-left: 5px;
        }

        &:nth-of-type(n + 3) {
          margin-top: 0;
        }
      }
    }

    &--name {
      @include fz(20, 27, 600);

      margin-top: 12px;

      @include media(sm) {
        margin-top: vwset(6);
      }
    }

    &--price {
      margin-top: 30px;

      small {
        color: #a0a0a0;
        margin-left: 5px;
        margin-bottom: 6px;
      }
    }

    &--form {
      display: flex;
      flex-wrap: wrap;
      margin-top: 40px;
    }

    &--formblock {
      width: 200px;

      &:nth-of-type(2n) {
        margin-left: 30px;
      }

      &:nth-of-type(n + 3) {
        margin-top: 10px;
      }

      .c-select {
        margin-top: 5px;
        width: 100%;

        &__display {
          height: 40px;
        }
      }

      .c-input {
        height: 40px;
        margin-top: 5px;
        text-align: right;
        width: 76px;
      }
    }

    &--button {
      align-items: flex-start;
      display: flex;
      flex-wrap: wrap;
      margin-top: 50px;
      width: 100%;

      .c-btn--cart {
        @include fz(16);
      }

      .c-btn--favorite {
        margin-left: 10px;
      }

      div {
        margin-top: 10px;
        text-align: center;
        width: 280px;

        .c-textlink--right {
          padding-right: 20px;
        }
      }
    }

    &--desc {
      margin-top: 70px;

      dd {
        margin-top: 20px;

        p {
          &:nth-of-type(n + 2) {
            margin-top: 30px;
          }
        }
      }
    }

    &--img {
      width: 430px;
    }

    &--mainimg {
      overflow: hidden;
      position: relative;

      .swiper-slide {
        width: 430px;
      }

      .swiper-button-prev,
      .swiper-button-next {
        background-color: rgba(#fff, .6);
        height: 45px;
        width: 45px;

        svg {
          width: 11px;
        }

        &:after {
          content: none;
        }
      }

      .swiper-button-prev {
        left: 0;
      }

      .swiper-button-next {
        right: 0;
      }
    }

    &--thumb {
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;

      div {
        margin-right: 16px;
        position: relative;
        width: 73px;

        &:nth-of-type(5n) {
          margin-left: 1px;
          margin-right: 0;
        }

        &:nth-of-type(n + 6) {
          margin-top: 16px;
        }

        img {
          width: 100%;
        }

        &.is-current {

          &:after {
            border: solid 2px #60a1d6;
            content: '';
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
          }
        }
      }
    }

    @include media(sm) {
      flex-direction: column-reverse;

      &--info {
        margin-top: vwset(30);
        width: auto;
      }

      .c-product-list__item--status {

        span {

          &:nth-of-type(n + 2) {
            margin-left: vwset(5);
          }
        }
      }

      &--name {
        margin-top: vwset(10);
      }

      &--price {
        margin-top: vwset(24);

        small {
          margin-left: vwset(5);
        }
      }

      &--form {
        display: block;
        margin-top: vwset(32);
      }

      &--formblock {
        width: auto;

        &:nth-of-type(2n) {
          margin-left: 0;
        }

        &:nth-of-type(n + 2) {
          margin-top: vwset(15);
        }

        .c-selectbox {
          margin-top: vwset(5);
        }

        .c-input {
          margin-top: vwset(5);
          width: vwset(80);
        }
      }

      &--button {
        margin-top: vwset(30);

        .c-btn--cart {
          flex-grow: 1;
          height: vwset(60);
        }

        .c-btn--favorite {
          height: vwset(60);
          margin-left: vwset(10);
          width: vwset(60);

          p {
            height: vwset(20);
            width: vwset(24);
          }
        }

        div {
          margin-top: vwset(10);
          width: calc(100% - 45px - #{vwset(10)});

          .c-textlink--right {
            padding-right: vwset(20);
          }
        }
      }

      &--desc {
        margin-top: vwset(56);

        dd {
          margin-top: vwset(20);

          p {

            &:nth-of-type(n + 2) {
              margin-top: vwset(20);
            }
          }
        }
      }

      &--img {
        width: 100%;
      }

      &--mainimg {

        .swiper-slide {
          width: auto;
        }

        .swiper-button-prev,
        .swiper-button-next {
          height: vwset(45);
          width: vwset(45);

          svg {
            width: vwset(11);
          }
        }
      }

      &--thumb {
        margin-top: vwset(20);

        div {
          margin-right: vwset(7.5);
          width: vwset(64);

          &:nth-of-type(5n) {
            margin-left: 0;
            margin-right: 0;
          }

          &:nth-of-type(n + 6) {
            margin-top: vwset(7.5);
          }
        }
      }
    }
  }

  // 

  &__stock {

    div {
      margin-top: 20px;
      overflow-x: auto;
    }

    table {
      min-width: 100%;

      th,
      td {
        border: solid 1px #e4e4e4;
        padding: 10px;
        text-align: center;
        white-space: nowrap;

        &.none {
          color: #e31616;
        }
      }

      thead {

        th,
        td {
          @include fz(14, 19, 600);
        }
      }

      tbody {

        th,
        td {
          @include fz(12, 18, 400);
        }
      }
    }

    @include media(sm) {

      div {
        margin-top: vwset(20);
      }

      table {

        th,
        td {
          padding: vwset(15);
        }
      }
    }
  }

  // 

  &__desc {
    margin-top: 37px;

    div {
      margin-top: 20px;

      p {
        &:nth-of-type(n + 2) {
          margin-top: 20px;
        }
      }
    }

    @include media(sm) {
      margin-top: vwset(60);

      div {
        margin-top: vwset(20);

        p {

          &:nth-of-type(n + 2) {
            margin-top: vwset(20);
          }
        }
      }
    }
  }

  // 

  &__list {
    margin-top: 75px;

    section {
      align-items: center;
      display: flex;
      justify-content: space-between;
      padding-right: 4px;

      .c-textlink--right {
        background-size: 4px auto;
        padding-right: 10px;
      }
    }

    .c-product-list--ranking {
      margin-top: 25px;
    }

    @include media(sm) {
      margin-top: vwset(60);

      section {
        padding-right: 0;

        .c-title--jpen {
          display: block;

          strong {
            display: block;
          }

          small {
            display: block;
            margin-left: 0;
          }
        }

        a {
          margin-top: vwset(5);
        }

        .c-textlink--right {
          background-size: vwset(4) auto;
          padding-right: vwset(10);
        }
      }

      .c-product-list--ranking {
        margin-top: vwset(15);
      }
    }
  }

  //Luminous
  .lum-lightbox.lum-open {
    z-index: 1000;
  }

  .lum-close-button {
    opacity: 1;
  }

  .lum-lightbox-inner img {

    @media screen and (max-width: 460px) {
      max-width: 160vw !important;  /*  */
      max-height: 85vh !important;  /*  */
    }
  }
}
