@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.login-user {

  .c-title--page {
    justify-content: center;
    & + .c-text--xs {
      text-align: center;
      margin-top: 35px;
    }

    @include media(sm) {
      & + .c-text--xs {
        margin-top: vwset(20);
      }
    }
  }

  &__check {
    margin-top: 40px;
    text-align: center;
  }

  &__beginner {
    text-align: center;
    margin-top: 37px;
  }

  &__area {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 37px 40px 38px;
    margin: 42px auto 0;
    width: 560px;
    nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 20px;
      a {
        margin-top: 7px;
      }
    }

    @include media(sm) {
      border-radius: 5px;
      padding: vwset(24);
      margin: vwset(36) auto 0;
      width: auto;
      nav {
        margin-top: vwset(24);
      }
    }
  }

  &__list {
    &:nth-of-type(n + 2) {
      margin-top: 18px;
    }
    dd {
      margin-top: 10px;
      input {
        width: 100%;
      }

      .password-wrap {
        position: relative;

        input {
          width: 100%;
          padding-right: 40px;
        }
        button {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          svg {
            fill: #a0a0a0 !important;
          }
        }

        @include media(sm) {
          width: 100%;
          margin-top: vwset(10);
        }
      }
    }

    .c-textlink {
      margin-top: 7px;
    }

    @include media(sm) {
      &:nth-of-type(n + 2) {
        margin-top: 18px;
      }
      dd {
        margin-top: 10px;
      }
    }
  }
}
