@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-title {

  //  (XD:h1)
  &--page {
    align-items: flex-end;
    display: flex;

    strong {
      @include fz(24, 41);
      @include letterSpacing(50);
      @include media(sm) {
        @include fz(20, 34);
      }
    }

    // 
    small {
      color: #a0a0a0;
      font-family: $fontFamilyEn;
      margin-left: 10px;
      margin-bottom: 8px;
      @include fz(14, 19);
      @include letterSpacing(100);

      @include media(sm) {
        margin-left: vwset(10);
      }
    }
  }

  // () (XD:h2)
  &--line {
    @include fz(20, 34);
    @include letterSpacing(50);
    padding-bottom: 10px;
    position: relative;

    &:after {
      background: linear-gradient(to right, #60a1d6 0%, #60a1d6 40px, #60a1d6 40px, #e4e4e4 40px, #e4e4e4 40px, #e4e4e4 100%);
      bottom: 0;
      content: '';
      height: 2px;
      left: 0;
      position: absolute;
      width: 100%;
    }
    @include media(sm) {
      color: #4aa2db;
      padding-bottom: 0;
      @include fz(18, 31);
      &:after {
        content: none;
      }
    }
  }

  // ()(XD: h3 )
  &--jpen {
    align-items: flex-end;
    display: flex;

    strong {
      @include fz(16, 27);
      @include letterSpacing(50);
    }

    small {
      color: #a0a0a0;
      font-family: $fontFamilyEn;
      margin-left: 10px;
      margin-bottom: 3px;
      @include fz(12, 21);
      @include letterSpacing(100);

      @include media(sm) {
        margin-left: vwset(10);
      }
    }

  }

  //  / 
  &--table {
    @include fz(14, 24);
    @include letterSpacing(50);

    &-label{
      line-height: 2.2;
    }
  }

  // 
  &--small-blue {
    span{
      color: #4aa2db;
      font-family: $fontFamilyEn;
      @include fz(14, 19);
      @include letterSpacing(100);
    }
  }

  //
  &--section {
    @include fz(20, 30);
    @include letterSpacing(50);
    @include media(sm) {
      @include fz(18, 27);
      @include letterSpacing(50);
    }
    &--gray {
      color: #A0A0A0;
      @include fz(14, 24);
      @include letterSpacing(50);
      margin-left: 5px;
      @include media(sm) {
        margin-left: vwset(5);
      }
    }
  }
}
