@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-margin {

  &--xxl {
    margin-top: 100px;
    @include media(sm) {
      margin-top: vwset(60);
    }
  }
  &--xl {
    margin-top: 80px;
    @include media(sm) {
      margin-top: vwset(64);
    }
  }
  &--l {
    margin-top: 60px;
    @include media(sm) {
      margin-top: vwset(48);
    }
  }
  &--m {
    margin-top: 40px;
    @include media(sm) {
      margin-top: vwset(32);
    }
  }
  &--s {
    margin-top: 30px;
    @include media(sm) {
      margin-top: vwset(24);
    }
  }
  &--xs {
    margin-top: 20px;
    @include media(sm) {
      margin-top: vwset(16);
    }
  }
  &--xxs {
    margin-top: 10px;
    @include media(sm) {
      margin-top: vwset(8);
    }
  }
  // l-container
  &--container {
    margin-top: 50px;
    @include media(sm) {
      margin-top: vwset(50);
    }
  }

}
