@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.guide-agreement {
  &__date {
    font-size: 1.4rem;
    text-align: right;
  }
  &__lead {
    font-size: 1.4rem;
    line-height: 1.8;
  }
  &__list {
    >.item {
      &__ttl {
        color: map-get($color,blue);
        font-size: 1.4rem;
        font-weight: bold;
        counter-increment: num;
        &::before {
          content: ''counter(num)'';
        }
        &::after {
          content: '';
        }
      }
      &__con {
        li {
          list-style: none;
        }
        >ul {
          >li {
            margin-left: 20px;
            font-size: 1.4rem;
            line-height: 1.8;
            text-indent: -20px;
            counter-increment: cnum;
            &::before {
              width: 20px;
              display: inline-block;
              text-indent: 0;
              content: counter(cnum)'.';
            }
            >ul {
              background: map-get($color, whitebg);
              margin-bottom: 20px;
              padding: 20px;
              @include media(sm) {
                margin-top: 8px;
                padding: 16px;
              }
              >li {
                padding-left: 20px;
                position: relative;
                font-size: 1.2rem;
                line-height: 1.5;
                text-indent: -20px;
                counter-increment: gcnum;
                &:not(:first-child) {
                  margin-top: 12px;
                }
                &::before {
                  width: 14px;
                  margin-right: 6px;
                  display: inline-block;
                  letter-spacing: -0.05em;
                  text-align: center;
                  text-indent: 0;
                  white-space: nowrap;
                  content: counter(gcnum, lower-roman);
                }

                &::after {
                  position: absolute;
                  top: 0;
                  left: 14px;
                  text-indent: 0;
                  content: '.';
                }
              }
            }
          }
        }
      }
    }
  }
}
