
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

//    (SP)
.c-product-info__sp {
  //
  &--order {
    list-style: none;
    padding-top: 20px;
    border-top: solid 1px #E4E4E4;
      > ul {
        display: flex;
      }
      > li {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        border-bottom: solid 1px #E4E4E4;
        &:last-of-type {
          border: none;
        }
        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
        }
        .image {
          width: vwset(80);
          min-width: vwset(80);
          img {
            width: 100%;
          }
        }
        .detail {
          width: calc(100% - #{vwset(92)});
          margin-left: vwset(12);
          strong {
            display: block;
            @include fz(12, 18, 700);
          }
          & > p {
            display: block;
            @include fz(12, 18, 400);
            margin-top: vwset(10);
          }
        }
        .total {
          display: flex;
          align-items: center;
          margin-top: vwset(6);
        }
        .price {
          @include fz(14, 19, 600);
          span {
            color: #a0a0a0;
            @include fz(12, 16, 400);
            margin-left: vwset(10);
          }
        }
        .count {
          margin-left: 30px;
        }
        .total_price {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: #f8f8f8;
          width: 100%;
          padding: vwset(8) vwset(10);
          margin-top: vwset(11);
          p {
            @include fz(12, 18, 400);
          }
        }
      }
      @include media(lg) {
        display: none;
      }

  }


  //
  &--purchase {
    list-style: none;
    padding-top: 20px;
    border-top: solid 1px #E4E4E4;
      > ul {
        display: flex;
      }
      > li {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        border-bottom: solid 1px #E4E4E4;
        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
        }
        .image {
          width: vwset(80);
          min-width: vwset(80);
          img {
            width: 100%;
          }
        }
        .detail {
          width: calc(100% - #{vwset(92)});
          margin-left: vwset(12);
          strong {
            display: block;
            @include fz(12, 18, 700);
          }
          & > p {
            display: block;
            @include fz(12, 18, 400);
            margin-top: vwset(10);
          }
        }
      }
      @include media(lg) {
        display: none;
      }

      .share {
        width: 100%;
        margin-top: 10px;
        padding: 10px;
        display: flex;
        align-items: center;
        background: #f8f8f8;
        &__list {
          width: 70%;
          display: flex;
          justify-content: space-around;
          list-style: none;
        }
        &__item {
          width: vwset(100);
          margin-right: 5px;;
          display: flex;
          flex-direction: column;
          align-items: center;
          &:last-of-type {
            margin-right: 0;
          }
          &-name {
            margin-top: 10px;
            text-align: center;
            font-family: $fontFamilyEn;
            color: #A0A0A0;
  
            @include media(sm) {
              margin-top: 5px;
            }
          }
        }
        &__icon {
          width: vwset(60);
          height: vwset(60);
          padding: vwset(14);
          border-radius: 5px;
          display: flex;
          justify-content: center;
          align-items: center;
          > svg {
            width: 30px;
            height: 30px;
            @include media(sm) {
              width: vwset(30);
              height: vwset(30);
            }
          }
          &--line {
            background: #00B900;
          }
          &--twitter {
            background: #1D9BF0;
          }
          &--facebook {
            background:#1877F2;
          }
        }
        &__text {
          width: 30%;
          text-align: left;
        }
      }
    }


  }
