@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.regist {

  .c-text--caution p {
    margin-top: 0;
  }

  .l-container {
    margin-top: 50px;

    @include media(sm) {
      margin-top: vwset(40);
    }
  }

  &__block {
    // margin-top: 60px;
    // h3 {
    //   margin-top: 40px;
    // }
    &--text {
      margin-top: 20px;
      &.glay {
        background-color: #f8f8f8;
        padding: 20px 15px;
      }
    }
    @include media(sm) {
      // margin-top: vwset(40);
      &--text {
        margin-top: vwset(20);
        &.glay {
          padding: vwset(20) vwset(15);
        }
      }
    }
  }

  &__rule {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 30px 15px;
    &--scroll {
      height: 340px;
      padding: 0 15px;
      overflow-y: auto;
    }
    time {
      display: block;
      text-align: right;
    }
    &--text {
      margin-top: 30px;
    }
    &--item {
      margin-top: 20px;
      ol {
        list-style-type: decimal;
        list-style-position: outside;
        margin-left: 1.5em;
        ol {
          list-style-type: lower-roman;
          margin-left: 1.5em;
        }
      }
    }
    aside {
      text-align: right;
      margin-top: 30px;
    }
    @include media(sm) {
      border-radius: vwset(5);
      padding: vwset(20) vwset(10);
      margin-top: vwset(20);
      &--scroll {
        height: vwset(360);
        padding: 0 vwset(10);
      }
      &--text {
        margin-top: vwset(30);
      }
      &--item {
        margin-top: vwset(20);
      }
      aside {
        margin-top: vwset(20);
      }
    }
  }

  &__complete {
    background-color: #f8f8f8;
    text-align: center;
    padding: 35px 85px;
    &--address {
      display: inline-block;
      background-color: #fff;
      padding: 15px 25px;
      margin-top: 20px;
      word-break: break-all;
    }
    @include media(sm) {
      padding: 20px;
      margin-top: vwset(10);
      &--address {
        padding: vwset(15) vwset(25);
        margin-top: vwset(20);
      }
    }
  }

  .c-btn-wrap--2btn {
    &.lg-rev {
      flex-direction: row-reverse;
    }
  }





}
