@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.mypage {
  &__title {
    width: 1200px;
    margin: 0 auto;

    @include media(sm) {
      width: auto;
      margin: 0 vwset(12);
    }
  }
  // &__container {
  //   margin-top: 60px;

  //   @include media(sm) {
  //     margin-top: vwset(30);
  //   }
  // }

  .c-title--line + .c-text--xs {
    margin-top: 20px;
  }



  &__top {
    nav {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      a {
        width: calc((100% - 40px) / 3);
        background-color: #f8f8f8;
        border-radius: 5px;
        margin-top: 20px;
        display: flex;
        align-items: center;
        height: 100px;
        padding: 0 18px 0 38px;
        transition: background-color $comMS $comES;
        &:hover {
          @include media(lg) {
            background-color: #e4e4e4;
          }
        }
        figure {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 32px;
          min-width: 32px;
          svg {
            width: 100%;
            fill: #60A1D6;
          }
        }
        dl {
          margin-left: 30px;
        }
      }
    }

    @include media(sm) {
      nav {
        display: block;
        a {
          width: auto;
          margin-top: vwset(20);
          display: flex;
          height: vwset(80);
          padding: 0 vwset(18);
          figure {
            width: vwset(26);
            min-width: vwset(26);
          }
          dl {
            margin-left: vwset(20);
          }
        }
      }
    }
  }




  // 

  &__abook {
    margin-top: 30px;

    &-search{
      .search__header {
        display: flex !important;
        margin-top: 0 !important;
        @include media(sm) {
          .search__header--pagecount {
            margin-top: 0 !important;
          }
        }
      }
    }


    table {
      width: 100%;

      thead {

        th {
          background-color: #f8f8f8;
          padding: 10px;

          &:nth-of-type(n + 2) {
            border-left: solid 2px #fff;
          }

          &.title {
            width: 160px;
          }

          &.name {
            width: 140px;
          }

          &.addr {
            width: 335px;
          }

          &.tel {
            width: 157px;
          }
        }
      }

      tbody {

        tr {
          border-bottom: solid 1px #e4e4e4;

          &:last-of-type {
            border-bottom: none;
          }
        }

        td {
          padding: 16px 20px 20px;
          &.button {
            text-align: center;
            button {
              width: 60px;
              height: 25px;
            }
          }
        }
      }
    }

    &--sm {
      list-style: none;

      li {
        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
          border-top: solid 1px #e4e4e4;
          padding-top: vwset(20);
        }
        dl {
          display: flex;
          &:nth-of-type(n + 2) {
            margin-top: vwset(2);
          }
          dt {
            background-color: #f8f8f8;
            white-space: nowrap;
            width: vwset(80);
            min-width: vwset(80);
            padding: vwset(5);
          }
          dd {
            padding: vwset(5);
            margin-left: vwset(10);
          }
        }
        nav {
          display: flex;
          justify-content: space-between;
          align-items: center;
          text-align: center;
          margin-top: vwset(10);
          .c-btn--change {
            width: vwset(80);
            height: vwset(30);
          }
          .c-textlink {
            color: #393939;
            text-decoration: underline;
          }
        }
      }

      @include media(lg) {
        display: none;
      }
    }

    @include media(sm) {
      margin-top: vwset(30);

      table {
        display: none;
      }
    }

    &--nodata {
      text-align: center;
    }
  }


  .c-btn-wrap--2btn {
    &.lg-rev {
      flex-direction: row-reverse;
    }
  }
  .l-form__list dd.birth-nowrap {
    @include media(sm){
      white-space: nowrap;
    }
  }

}
