@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.cart {

  // 

  .c-step {
    margin-top: 34px;

    @include media(sm) {
      margin-top: vwset(14);
    }
  }

  // 

  .c-text--caution {
    margin-top: 48px;

    @include media(sm) {
      margin-top: vwset(30);
    }
  }

  // 

  &__navigation {
    align-items: center;
    display: flex;
    justify-content: center;
    position: relative;

    .c-textlink--left {
      left: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .c-btn--blue {
      @include fz(16, 21, 600);

      margin: 0 15px;
    }

    @include media(sm) {
      display: block;
      text-align: center;

      .c-textlink--left {
        left: 0;
        margin-top: vwset(20);
        position: static;
        top: 0;
        transform: translateY(0);
      }

      .c-btn--blue {
        display: flex;
        margin: 0;
        margin-top: vwset(10);
        width: auto;
      }
    }
  }

  // 

  .product-detail__list {

    &.cart__related {
      margin-top: 120px;
    }

    @include media(sm) {

      &.cart__related {
        margin-top: vwset(96);
      }
    }
  }

  .c-product-list--relation {

    .c-product-list__item {
      margin-right: 38px;

      &:nth-of-type(5n) {
        margin-right: 38px;
      }

      &:nth-of-type(6n) {
        margin-right: 0;
      }

      &:nth-of-type(n + 6) {
        margin-top: 20px;
      }

      &:nth-of-type(n + 7) {
        margin-top: 60px;
      }
    }

    @include media(sm) {

      .c-product-list__item {
        margin-right: 0;

        &:nth-of-type(5n) {
          margin-right: 0;
        }

        &:nth-of-type(6n) {
          margin-right: 0;
        }

        &:nth-of-type(n + 6) {
          margin-top: 20px;
        }

        &:nth-of-type(n + 7) {
          margin-top: 20px;
        }
      }
    }
  }

  // 

  &__nologin {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 35px 60px 60px;

    & > p {
      @include fz(13, 17, 400);

      margin-top: 17px;
    }

    ul {
      @include fz(13, 17, 400);

      list-style: none;
      margin-top: 26px;

      li {
        padding-left: 1em;
        position: relative;

        &:before {
          content: '';
          left: 0;
          position: absolute;
          top: 0;
        }
      }
    }

    strong {
      display: block;
      margin-top: 17px;

      @include fz(16, 21, 600);
    }

    ol {
      display: flex;
      margin-top: 10px;

      @include fz(13, 17, 400);

      li {

        &:nth-of-type(n + 2) {
          @include icon('arrow-right', '4aa2db');

          background-position: left center;
          background-size: 6px auto;
          margin-left: 20px;
          padding-left: 20px;
        }
      }
    }

    nav {
      margin-top: 40px;
      text-align: center;

      .c-btn--blue {
        @include fz(16, 21, 600);

        margin: 0 15px;
      }
    }

    @include media(sm) {
      border-radius: vwset(5);
      padding: vwset(20);

      & > p {
        @include fz(12);

        margin-top: vwset(15);
      }

      ul {
        @include fz(12);

        margin-top: vwset(20);
      }

      strong {
        @include fz(14);

        margin-top: vwset(15);
      }

      ol {
        display: block;
        margin-top: vwset(10);

        @include fz(12);

        li {

          &:nth-of-type(n + 2) {
            background: none;
            margin-left: 0;
            padding-left: 0;
          }
        }
      }

      nav {
        margin-top: vwset(20);
      }
    }
  }

  // 

  &__list {

    &--products {
      width: 100%;

      thead {

        th {
          background-color: #f8f8f8;
          padding: 10px;

          &:nth-of-type(n + 2) {
            border-left: solid 2px #fff;
          }

          &.name {
            width: 350px;
          }

          &.price {
            width: 180px;
          }

          &.count {
            width: 180px;
          }

          &.total {
            width: 180px;
          }

          &.memo {
            width: 220px;
          }
        }
      }

      tbody {

        tr {
          border-bottom: solid 1px #e4e4e4;

          &:last-of-type {
            border-bottom: none;
          }
        }

        td {
          padding: 16px 20px 20px;

          span {
            color: #a0a0a0;
            margin-left: 5px;

            @include fz(13, 17, 400);
          }

          &.name {
            padding: 16px 20px 20px 0;
          }

          &.price {
            @include fz(20, 27, 600);

            text-align: right;
          }

          &.count {
            padding: 16px 16px 20px;
            text-align: center;

            .c-input {
              height: 40px;
              text-align: right;
              width: 76px;
            }

            .c-btn--change {
              height: 25px;
              margin-left: 10px;
              width: 60px;
            }
          }

          &.total {
            @include fz(20, 27, 600);

            text-align: right;
          }

          &.memo {

            p {
              @include fz(12, 18, 400);
            }

            .c-btn--add-fav {
              margin-top: 10px;
            }
          }

          &.delete {

            .c-btn--black {
              height: 25px;
              width: 60px;
            }
          }
        }
      }
    }

    &--name {
      align-items: center;
      display: flex;

      a {
        min-width: 120px;
        overflow: hidden;
        width: 120px;

        &:hover {
          @include media(lg) {

            img {
              opacity: .5;
              transform: scale(1.5);
            }
          }
        }

        img {
          height: 120px;
          transform-origin: center;
          transition: opacity $comMS $comES, transform $comMS $comES;
          width: 120px;

          @include object-fit(cover, center);
        }
      }

      div {
        margin-left: 20px;

        strong {
          display: block;

          @include fz(14, 19, 700);
        }

        p {
          @include fz(12, 18, 400);

          margin-top: 10px;
        }
      }
    }

    &--total {
      width: 100%;

      tr {

        &:nth-of-type(n + 2) {
          border-top: solid 2px #fff;
        }
      }

      th,
      td {
        background-color: #f8f8f8;
        padding: 21px 20px;
        text-align: right;
      }

      th {
        width: 960px;

        @include fz(16, 21, 600);
      }

      td {
        @include fz(20, 27, 600);

        border-left: solid 2px #fff;
      }
    }

    &--products-sm {
      list-style: none;

      li {
        align-items: flex-start;
        display: flex;
        flex-wrap: wrap;

        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
        }

        .image {
          min-width: vwset(80);
          width: vwset(80);

          img {
            width: 100%;
          }
        }

        .detail {
          margin-left: vwset(12);
          width: calc(100% - #{vwset(92)});

          strong {
            display: block;

            @include fz(12, 18, 700);
          }

          & > p {
            display: block;
            margin-top: vwset(10);

            @include fz(12, 18, 400);
          }
        }

        .total {
          align-items: center;
          display: flex;
          justify-content: space-between;
          margin-top: vwset(6);
        }

        .price {
          @include fz(14, 19, 600);

          span {
            color: #a0a0a0;
            margin-left: vwset(10);

            @include fz(12, 16, 400);
          }
        }

        .count {

          .c-input {
            height: vwset(30);
            padding: vwset(5) vwset(10);
            text-align: right;
            width: vwset(60);

            @include fz(16, 21, 400);
          }

          .c-btn--change {
            height: vwset(26);
            margin-left: vwset(10);
            width: vwset(60);
          }
        }

        nav {
          align-items: center;
          display: flex;
          justify-content: space-between;
          margin-top: vwset(18);
          text-decoration: underline;
          width: 100%;

          @include fz(12, 16, 400);

          button {

            &:first-of-type {
              opacity: .6;
            }
          }
        }

        .total_price {
          align-items: center;
          background-color: #f8f8f8;
          display: flex;
          justify-content: space-between;
          margin-top: vwset(11);
          padding: vwset(8) vwset(10);
          width: 100%;

          p {
            @include fz(12, 18, 400);
          }
        }
      }

      @include media(lg) {
        display: none;
      }
    }

    aside {
      @include fz(12, 20, 400);
    }

    @include media(sm) {
      margin-top: vwset(30);

      &--products {
        display: none;
      }

      &--name {

        a {
          min-width: vwset(100);
          width: vwset(100);

          img {
            height: vwset(100);
            width: vwset(100);
          }
        }

        div {
          margin-left: vwset(15);

          strong {
            @include fz(13);
          }

          p {
            @include fz(11);

            margin-top: vwset(10);
          }
        }
      }

      &--total {
        margin-top: vwset(30);

        th,
        td {
          padding: vwset(15);
        }

        th {
          width: vwset(200);

          @include fz(14);
        }

        td {
          @include fz(18);
        }
      }

      aside {
        @include fz(11);
      }
    }
  }
}
