@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-search {
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  transition: opacity $comMS $comES, visibility $comMS $comES;
  visibility: hidden;
  width: 100%;
  z-index: 999;

  .is-c-search-active & {
    opacity: 1;
    visibility: visible;
  }

  &__back {
    background-color: rgba(#000, .5);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    will-change: opacity;
    z-index: -1;
  }

  &__title {
    align-items: center;
    background-color: #e8e8e8;
    display: flex;
    height: vwset(70);
    justify-content: center;
    position: relative;

    p {
      align-items: center;
      color: #60a1d6;
      display: flex;

      @include fz(16, 21, 600);

      svg {
        margin-right: vwset(10);
        width: vwset(17);
      }
    }

    button {
      height: vwset(70);
      position: absolute;
      right: 0;
      top: 0;
      width: vwset(56);

      span {
        background-color: #a0a0a0;
        border-radius: vwset(4);
        height: vwset(2);
        left: vwset(18);
        position: absolute;
        transform-origin: center;
        width: vwset(24);

        &:nth-of-type(1) {
          top: calc(50% - #{vwset(2)} / 2);
          transform: rotate(-315deg);
        }

        &:nth-of-type(2) {
          bottom: calc(50% - #{vwset(2)} / 2);
          transform: rotate(315deg);
        }
      }
    }
  }

  form {
    background-color: #fff;
    max-height: 100%;
    overflow-y: auto;
    padding-bottom: vwset(35);
  }

  &__block {
    border-bottom: solid 1px #e4e4e4;
    margin: vwset(20) vwset(20) 0;
    padding-bottom: vwset(25);

    &--title {
      @include fz(16, 21, 600);

      padding-bottom: 10px;
    }

    div {
      align-items: center;
      display: flex;

      &:nth-of-type(n + 2) {
        margin-top: 10px;
      }

      span {
        @include fz(14, 19, 600);

        margin-right: 10px;
        white-space: nowrap;
      }
    }
  }

  .c-input {
    height: vwset(40);
    width: 100%;
  }

  .c-selectbox {
    display: block;
  }

  nav {
    margin: vwset(30) vwset(20) 0;

    .c-btn--search {
      width: 100%;
    }

    .c-btn--simple {
      margin-top: vwset(15);
      width: 100%;
    }
  }
}
