@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.l-breadcrumbs {
  @include width(1200px, 20px, vwset(12));
  @include fz(13, 18, 400);



  @include media(sm) {
    @include fz(12);
    padding: vwset(1) 0;
    overflow-x: auto;
  }

  &__inner {
    width: 100%;
  }

  &__list {
    align-items: center;
    display: flex;

    svg {
      margin: 0 14px;
      min-width: 6px;
      width: 6px;
      height: 11px;
    }

    @include media(sm) {

      svg {
        margin: 0 vwset(10);
        min-width: vwset(4);
        width: vwset(4);
      }
    }
  }

  &__item {
    white-space: nowrap;

    a {
      color: #a0a0a0;
      transition: opacity $comMS $comES;

      &:hover {
        @include media(lg) {
          opacity: .5;
        }
      }
    }
  }
}
