@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;


.history-list {

    &__block {
      width: 100%;
      background: #F8F8F8;
      border-radius: 5px;
      //
      .status {
        display: flex;
        justify-content: space-between;
        padding: 20px;
        @include media(sm) {
          padding: vwset(20);
        } 
        .c-btn--blue {
          width:  95px;
          height: 25px;
          @include media(sm) {
            width: vwset(95);
            height: vwset(25);
            @include fz(13, 20);
            @include letterSpacing(100);
          }
        }
      } 

      //
      .desc {
        padding: 20px;
        display: flex;
        border-top: solid 1px #E4E4E4;
        &:last-of-type {
          margin: 0;
        }
        @include media(sm) {
          display: block;
          padding: vwset(20);
        } 
        &__list {
          margin-right: 100px;
          @include media(sm) {
            margin-top: vwset(10);
            margin-right: 0;
            display: flex;
            justify-content: space-between;
          } 
          &:first-child {
            @include media(sm) {
              margin-top: 0;
            } 
          }
        }
      }

    }

  }