@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;


.c-history-info {
    &-2col {
        display: flex;
        justify-content: space-between;
        @include media(sm){
            display: block;
        }
    }
    &-item {
        width: 460px;
        background-color: #f8f8f8;
        border-radius: 5px;
        @include media(sm){
            width: 100%;
            &:last-child {
                margin-top: vwset(20);
            }
        }
        &:first-of-type {
            @include media(sm){
                margin-top: 0;
            }
        }
        &__title {
            padding: 16px 20px 14px;
        }
    }
    &-list {
        padding: 24px 20px;
        border-top: 1px solid #E4E4E4;
        @include media(sm){
            padding: 24px 15px;
        }
        dl {
            display: flex;
            // align-items: center;
            justify-content: flex-start;
            margin-top: 20px;
            &:first-of-type {
                margin-top: 0;
            }
        }
        &__title {
            width: 155px;
            @include media(sm){
                width: 45%;
            }
        }
        &__detail {
            width: calc(100% - 155px);
            word-break: break-all;
            @include media(sm){
                width: 55%;
            }
        }
        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100px;
            height: 30px;
            border-radius: 3px;
            border: 1px solid #2369ba;
            color: #2369ba;
            @include media(sm){
                width: vwset(100);
                height: vwset(30);
            }
            &.orange-label {
                color: #E07409;
                border: solid 1px #D2792F;
            }
        }
    }

    .price-list {

        &__item {
            display: flex;
            align-items: center;
            width: 100%;
        }

        dt {
            background: #f8f8f8;
            display: flex;
            justify-content: flex-start;
            width: 960px;
            @include media(sm) {
            width: 50%;
            }
        }

        dd {
            align-items: center;
            display: flex;
            justify-content: flex-end;
            width: 240px;

            @include media(sm) {
            width: 50%;
            }
        }

        &__wrap {
            display: flex;
            align-items: flex-start;
        }

        &__inner-wrap {

            @include media(sm) {
            width: 50%;
            }

            > dd {
            @include media(sm) {
                width: 100%;
            }
            }
        }

        &__inner-item {
            align-items: flex-end;
            display: flex;
            justify-content: flex-end;

            &--left {
            max-width: 120px;
            width: 100%;
            @include media(sm) {
                max-width: initial;
                width: 62%;
            }
            }

            &--right {
            max-width: 90px;
            width: 100%;
            @include media(sm) {
                max-width: initial;
                width: 38%;
            }
            }
        }

        &__total {
            color: #e31616;
        }

    }


}
