@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-product-list {
  // 

  &--thumb {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;

    .c-product-list__item {
      margin-right: 28px;
      width: 219px;

      &:nth-of-type(4n) {
        margin-right: 0;
      }

      &:nth-of-type(n + 5) {
        margin-top: 60px;
      }

      .c-product-list__item--image {
        height: 219px;
      }
    }

    @include media(sm) {
      justify-content: space-between;

      .c-product-list__item {
        margin-right: 0;
        width: vwset(170);

        &:nth-of-type(n + 3) {
          margin-top: vwset(30);
        }

        .c-product-list__item--image {
          height: vwset(170);
        }
      }
    }
  }

  // 

  &--ranking {

    .swiper-wrapper {
      align-items: flex-start;
      display: flex;
      flex-wrap: wrap;

      .swiper-slide {
        margin-right: 30px;
        width: 168px;

        &:nth-of-type(5n) {
          margin-right: 0;
        }

        &:nth-of-type(n + 6) {
          margin-top: 60px;
        }
      }

      .c-product-list__item {

        .c-product-list__item--image {
          height: 168px;
        }
      }
    }

    @include media(sm) {
      overflow-x: hidden;
      padding: 0 vwset(12) vwset(40);
      position: relative;
      width: 100%;

      .swiper-wrapper {
        flex-wrap: nowrap;

        .swiper-slide:first-of-type {
          border-bottom-left-radius: 10px;
          border-top-left-radius: 10px;
        }

        .swiper-slide {
          background: #f8f8f8;
          margin-right: 0;
          padding: vwset(30) vwset(5);
          //background-color: #f8f8f8;
          width: vwset(166);

          &:first-of-type {
            padding-left: vwset(10);
          }

          &:nth-of-type(5n) {
            margin-right: 0;
          }

          &:nth-of-type(n + 6) {
            margin-top: 0;
          }
          /*
          &:first-of-type {
            width: vwset(171);
            padding-left: vwset(10);
            border-radius: vwset(7) 0 0 vwset(7);
          }
          &:last-of-type {
            width: vwset(171);
            padding-right: vwset(10);
            border-radius: 0 vwset(7) vwset(7) 0;
          }
          */
        }

        .c-product-list__item {

          .c-product-list__item--image {
            height: vwset(156);
          }

          .c-product-list__item--status {
            width: vwset(146);

            span {
              padding: vwset(3) 0;
              width: vwset(70);
            }
          }
        }
      }

      .swiper-pagination {
        bottom: 0 !important;

        .swiper-pagination-bullet {
          height: vwset(8);
          margin: 0 vwset(8) !important;
          width: vwset(8);
        }
      }
    }
  }

  // 

  &--relation {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;

    .c-product-list__item {
      margin-right: 30px;
      margin-top: 20px;
      width: 168px;

      &:nth-of-type(5n) {
        margin-right: 0;
      }

      &:nth-of-type(n + 6) {
        margin-top: 60px;
      }

      .c-product-list__item--image {
        height: 168px;
      }
    }

    @include media(sm) {
      justify-content: space-between;

      .c-product-list__item {
        margin-right: 0;
        margin-top: vwset(20);
        width: vwset(168);

        &:nth-of-type(5n) {
          margin-right: 0;
        }

        &:nth-of-type(n + 6) {
          margin-top: 0;
        }

        .c-product-list__item--image {
          height: vwset(168);
        }

        .c-product-list__item--status {
          width: vwset(156);

          span {
            padding: vwset(3) 0;
            width: vwset(74);
          }
        }
      }
    }
  }

  // 

  &--list {

    .c-product-list__item {
      align-items: center;
      border-bottom: solid 1px #e4e4e4;
      display: flex;
      padding-bottom: 20px;

      &--image {
        min-width: 168px;
        width: 168px;
      }

      &--title {
        margin-top: 0;
      }

      form {
        width: 230px;
        min-width: 230px;
        margin-left: 30px;

        .c-btn--favorite {
          margin-left: auto;
          margin-right: 0;
        }
      }

      &:nth-of-type(n + 2) {
        margin-top: 20px;
      }
    }

    @include media(sm) {

      .c-product-list__item {
        flex-wrap: wrap;
        align-items: flex-start;
        padding-bottom: vwset(20);

        &--image {
          min-width: vwset(120);
          width: vwset(120);
        }

        &--status {
          margin-top: vwset(4);
          position: static;
          width: vwset(120);

          span {
            @include fz(10);

            padding: vwset(3) 0;
            width: vwset(58);

            &:nth-of-type(n + 3) {
              margin-top: vwset(4);
            }
          }
        }

        &--info {
          width: calc(100% - #{vwset(140)});
        }

        form {
          display: flex;
          align-items: center;
          width: 100%;
          min-width: initial;
          margin: vwset(10) 0 0;
          .c-btn--cart {
            width: calc(100% - 50px);
          }
        }

        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
        }
      }
    }
  }

  // 

  &__item {
    display: block;
    text-decoration: none;

    &:hover {
      @include media(lg) {

        .c-product-list__item--image {

          img {
            opacity: .5;
            transform: scale(1.5);
          }
        }

        .c-product-list__item--title,
        .c-product-list__item--text,
        .c-product-list__item--price {
          opacity: .5;
        }
      }
    }

    &--image {
      display: block;
      overflow: hidden;
      position: relative;

      img {
        @include object-fit(cover, center);

        transform-origin: center;
        transition: opacity $comMS $comES, transform $comMS $comES;
        width: 100%;
      }
    }

    &--rank1,
    &--rank2,
    &--rank3,
    &--rank4,
    &--rank5 {
      align-items: center;
      color: #fff;
      display: flex;
      height: 28px;
      justify-content: center;
      left: 0;
      position: absolute;
      top: 0;
      width: 28px;

      @include fz(16, 22, 600);
    }

    &--rank1 {
      background-color: #e0bf09;
    }

    &--rank2 {
      background-color: #a0a0a0;
    }

    &--rank3 {
      background-color: #9f5a16;
    }

    &--rank4 {
      background-color: #393939;
    }

    &--rank5 {
      background-color: #393939;
    }

    &--badge {
      align-items: center;
      background-color: rgba(#fff, .4);
      border: solid 2px #e31616;
      border-radius: 50%;
      color: #e31616;
      display: flex;
      font-family: $fontFamilyEn;
      height: 42px;
      justify-content: center;
      position: absolute;
      right: 6px;
      top: 6px;
      width: 42px;

      @include fz(13, 18, 700);
    }

    &--status {
      bottom: 6px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      left: 6px;
      position: absolute;
      width: 156px;

      span {
        border-radius: 2px;
        color: #fff;
        padding: 3px;
        text-align: center;
        width: 75px;

        @include fz(12, 16, 600);
        @include letterSpacing(100);

        &:nth-of-type(n + 3) {
          margin-top: 6px;
        }

        &.red {
          background-color: #e31616;
        }

        &.green {
          background-color: #51ba23;
        }

        &.blue {
          background-color: #2369ba;
        }

        &.orange {
          background-color: #e07409;
        }
      }
    }

    &--info {
      flex-grow: 1;
      margin-left: 30px;
    }

    &--title {
      display: block;
      margin-top: 15px;
      transition: opacity $comMS $comES;

      @include fz(14, 19, 700);
    }

    &--text {
      @include fz(12, 18);
      @include letterSpacing(50);

      margin-top: 10px;
      transition: opacity $comMS $comES;
    }

    &--price {
      @include fz(16, 21, 600);

      margin-top: 10px;
      transition: opacity $comMS $comES;

      &.red {
        color: #e31616;
      }

      .c-product-list__item--title + & {
        margin-top: 5px;
      }
    }

    form {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      .c-btn--cart {
        width: calc(100% - 50px);
      }
      .c-btn--favorite {
        margin-left: auto;
        margin-right: 0;
      }
    }

    @include media(sm) {

      &--rank1,
      &--rank2,
      &--rank3,
      &--rank4,
      &--rank5 {
        height: vwset(28);
        width: vwset(28);
      }

      &--badge {
        @include fz(12, 16, 700);

        height: vwset(40);
        right: vwset(5);
        top: vwset(5);
        width: vwset(40);
      }

      &--status {
        bottom: vwset(6);
        left: vwset(6);
        width: vwset(156);

        span {
          border-radius: vwset(2);
          padding: vwset(3);
          width: vwset(75);

          &:nth-of-type(n + 3) {
            margin-top: vwset(6);
          }
        }
      }

      &--info {
        margin-left: vwset(20);
      }

      &--title {
        margin-top: vwset(15);
      }

      &--text {
        margin-top: vwset(10);
      }

      &--price {
        margin-top: vwset(10);

        .c-product-list__item--title + & {
          margin-top: vwset(5);
        }
      }

      form {
        display: block;
        text-align: center;
        margin-top: vwset(10);
        .c-btn--cart {
          width: 100%;
        }
        .c-btn--add-fav {
          width: 100%;
          height: vwset(40);
          margin-top: vwset(5);
          svg {
            width: 16px;
          }
        }
      }
    }
  }
}
