@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.l-header {
  background-color: #fff;
  border-bottom: none;
  left: 0;
  position: fixed;
  top: 0;
  transition: height $comMS $comES;
  width: 100%;
  z-index: 500;

  &__inner {
    display: flex;
    height: 90px;
    transition: height $comMS $comES;

    @include width(1200px, 20px, 0);
  }

  &__logo {
    align-items: center;
    display: flex;
    margin-right: auto;
    transition: opacity $comMS $comES;
    width: 140px;

    &:hover {
      @include media(lg) {
        opacity: .5;
      }
    }

    h1 {
      align-items: center;
      display: flex;

      img {
        width: 100%;
      }
    }
  }

  &__search {
    align-items: center;
    display: flex;
    position: relative;

    input {
      background-color: #f8f8f8;
      border: solid 1px #e4e4e4;
      border-radius: 20px;
      height: 35px;
      outline: none;
      padding: 0 45px 0 20px;
      transition: border-color $comMS $comES;
      width: 350px;

      @include fz(13, 17, 500);

      &:hover,
      &:focus {
        @include media(lg) {
          border-color: #a0a0a0;
        }
      }

      @include placeholder() {
        color: #a0a0a0;
      }
    }

    button {
      align-items: center;
      display: flex;
      height: 35px;
      justify-content: center;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 45px;

      svg {
        width: 16px;
      }
    }
  }

  // 

  .is-scroll & {
    border-bottom: solid 1px #e4e4e4;

    &__inner {
      height: 70px;
    }
  }

  @include media(sm) {

    &__inner {
      height: vwset(70);
      justify-content: space-between;
    }

    &__logo {
      margin: 0 0 0 vwset(24);
      order: 2;
      width: vwset(140);
    }

    // 

    .is-scroll & {

      &__inner {
        height: vwset(70);
      }
    }
  }

  // 

  &-search {
    left: 0;
    opacity: 0;
    padding: vwset(15) vwset(13);
    position: fixed;
    top: vwset(70);
    transition: opacity $comMS $comES, visibility $comMS $comES;
    visibility: hidden;
    width: 100%;
    z-index: 300;

    .is-search-active & {
      opacity: 1;
      visibility: visible;
    }

    div {
      background-color: rgba(#000, .5);
      height: 100%;
      left: 0;
      position: fixed;
      top: 0;
      width: 100%;
      will-change: opacity;
      z-index: -1;
    }

    form {
      align-items: center;
      display: flex;
      left: 50%;
      position: relative;
      transform: scale(.8125) translateX(-50% * 1.23);
      transform-origin: center;
      width: vwset(350 * 1.23);

      input {
        background-color: #f8f8f8;
        border: solid 1px #e4e4e4;
        border-radius: vwset(20 * 1.23);
        height: vwset(40 * 1.23);
        outline: none;
        padding: 0 vwset(45 * 1.23) 0 vwset(20 * 1.23);
        transition: border-color $comMS $comES;
        width: 100%;

        @include fz(16, 20, 600);

        &:hover,
        &:focus {
          @include media(lg) {
            border-color: #a0a0a0;
          }
        }

        @include placeholder() {
          color: #a0a0a0;
        }
      }

      button {
        align-items: center;
        display: flex;
        height: vwset(40 * 1.23);
        justify-content: center;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: vwset(45 * 1.23);

        svg {
          width: vwset(16 * 1.23);
        }
      }
    }
  }
}
