
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.c-before-info {
  background: #f8f8f8;
  padding: 30px 60px 60px;

  @include media(sm) {
    padding: 20px 30px 30px;
  }

  ul {
    list-style: none;
  }

  .list {
    margin-top: 10px;
  }

  .list_note {
    margin-top: 10px;
  }

  &__title {
    @include media(sm) {
      margin-top: 10px;
    }
  }

  .step-title {
    margin-top: 20px;

    @include media(sm) {
      margin-top: 20px;
    }
  }

  .step {
    display: flex;
    flex-wrap: wrap;

    @include media(sm) {
      flex-direction: column;
    }

    &__inner {
      margin: 5px 40px 0 0;
      width: calc((100% - 80px) / 3);

      @include media(sm) {
        margin-top: vwset(10);
        width: 100%;
      }

      &:nth-of-type(3n) {
        margin-right: 0;

        @include media(sm) {
          margin: 20px 0 0 0;
        }
      }
    }
    &__inner:not(:nth-child(-n+3)) {
      margin-top: 20px;
    }
  }
}
