
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.c-btn-wrap {
  align-items: center;
  display: flex;
  flex-direction: column;

  &__text {
    margin-bottom: 20px;

    @include media(sm) {
      text-align: center;
    }
  }

  &--2btn {
    display: flex;
    justify-content: center;
    align-items: center;
    @include media(sm) {
      flex-wrap: wrap;
    }
    .c-btn--simple, .c-btn--blue {
      @include fz(16, 16, 600);
      width: 280px;
      height: 45px;
      margin: 0 15px;
      cursor: pointer;
      &:first-child {
        @include media(sm) {
          margin-top: 0;
        }
      }
      @include media(sm) {
        width: 100%;
        height: vwset(45);
        margin: vwset(15) 0 0;
      }
    }

  }

}

.c-btn-wrap--2btn {
  &.lg-rev {
    flex-direction: row-reverse;
  }
}