@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.guest-inquiry {
  .c-history-info-item {
    width: 580px;
    @include media(sm){
      width: 100%;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    &--block {
      width: 580px;
      background-color: #f8f8f8;
      padding-bottom: 35px;
      border-radius: 5px;
      h3 {
        padding: 15px 20px;
        border-bottom: solid 1px #e4e4e4;
      }
      dl {
        display: flex;
        align-items: center;
        margin: 25px 20px 0;
        dt {
          width: 160px;
          min-width: 160px;
          white-space: nowrap;
        }
        dd {
          span {
            display: inline-block;
            color: #E07409;
            padding: 3px 15px;
            border: solid 1px #D2792F;
            border-radius: 3px;
          }
        }
      }
    }

    @include media(sm) {
      display: block;
      &--block {
        width: auto;
        padding-bottom: 35px;
        border-radius: 5px;
        &:nth-of-type(n + 2) {
          margin-top: vwset(20);
        }
        h3 {
          padding: vwset(10) vwset(20);
        }
        dl {
          margin: vwset(15) vwset(20) 0;
          dt {
            width: vwset(130);
            min-width: vwset(130);
          }
          dd {
            span {
              padding: vwset(3) vwset(10);
              border-radius: 3px;
            }
          }
        }
      }
    }
  }

  &__detail {
    ul {
      list-style: none;
      li {
        display: flex;
        align-items: flex-start;
        position: relative;
        &:nth-of-type(n + 2) {
          margin-top: 40px;
        }
        &:last-of-type {
          &:before {
            content: none;
          }
        }
        &:before {
          content: '';
          width: 2px;
          height: calc(100% + 40px);
          background-color: #e4e4e4;
          position: absolute;
          top: 0;
          left: 24px;
          z-index: -1;
        }
        img {
          width: 50px;
          min-width: 50px;
        }
        div {
          background-color: #f8f8f8;
          border: solid 1px #e4e4e4;
          border-radius: 5px;
          margin-left: 10px;
          h4 {
            display: flex;
            align-items: flex-start;
            color: #60A1D6;
            @include fz(18, 26, 600);
            @include letterSpacing(50);
            margin: 17px 30px 0;
            &:before {
              content: '';
              width: 2px;
              height: 20px;
              background-color: #60A1D6;
              margin: 2px 7px 0 0;
            }
          }
          dl {
            dt {
              display: flex;
              justify-content: space-between;
              align-items: center;
              border-bottom: solid 1px #e4e4e4;
              padding: 10px 50px 10px 30px;
              time {
                background: url(../images/icon/clock.svg) no-repeat left center/12px;
                padding-left: 20px;
              }
            }
            dd {
              padding: 15px 30px;
            }
          }
        }
      }
    }

    @include media(sm) {
      ul {
        li {
          &:nth-of-type(n + 2) {
            margin-top: vwset(30);
          }
          &:before {
            height: calc(100% + #{vwset(30)});
            left: vwset(19);
          }
          img {
            width: vwset(40);
            min-width: vwset(40);
          }
          div {
            margin-left: vwset(10);
            h4 {
              @include fz(16);
              margin: vwset(15) vwset(20) 0;
              &:before {
                height: vwset(16);
                margin: vwset(3) vwset(7) 0 0;
              }
            }
            dl {
              dt {
                display: block;
                padding: vwset(10) vwset(20);
                time {
                  display: block;
                  padding-left: vwset(20);
                }
              }
              dd {
                padding: vwset(15) vwset(20);
              }
            }
          }
        }
      }
    }
  }






  &__area {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 37px 40px 38px;
    nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
    }

    @include media(sm) {
      border-radius: 5px;
      padding: vwset(24);
      margin: 0 auto;
      nav {
        margin-top: vwset(30);
      }
    }
  }


  &__list {
    &:nth-of-type(n + 2) {
      margin-top: 18px;
    }
    dd {
      margin-top: 10px;
      input {
        width: 100%;
      }
    }

    @include media(sm) {
      &:nth-of-type(n + 2) {
        margin-top: 18px;
      }
      dd {
        margin-top: 10px;
      }
    }
  }
}
