@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/icon" as *;

.c-btn {
  align-items: center;
  background-color: #4aa2db;
  border-radius: 30px;
  color: #fff;
  display: inline-flex;
  height: 40px;
  justify-content: center;
  outline: none;
  transition: color $comMS $comES, background-color $comMS $comES;

  @include fz(13, 13, 600);

  &:hover {
    @include media(lg) {
      background-color: #3669b4;
    }
  }

  @include media(sm) {
    @include fz(16, 20, 600);

    border-radius: vwset(30);
    height: vwset(50);
  }

  // 

  &--readmore {
    @extend .c-btn;

    @include icon('arrow-right', 'ffffff');

    background-position: right 13px center;
    background-size: 6px auto;
    width: 150px;

    @include media(sm) {
      background-position: right vwset(20) center;
      background-size: vwset(6) auto;
      width: vwset(230);
    }
  }

  // 

  &--search {
    @extend .c-btn;

    height: 35px;
    width: 150px;

    p {
      @include icon('search', 'ffffff');

      background-position: left center;
      background-size: 14px auto;
      padding-left: 20px;
    }

    @include media(sm) {
      width: vwset(230);
    }
  }

  // 

  &--cart {
    @extend .c-btn;

    height: 45px;
    width: 280px;

    p {
      @include icon('cart', 'ffffff');

      background-position: left center;
      background-size: 22px auto;
      padding-left: 28px;
    }

    @include media(sm) {
      width: vwset(230);
    }
  }

  // 

  &--favorite {
    align-items: center;
    background-color: #f8f8f8;
    border: solid 1px #e4e4e4;
    border-radius: 50%;
    display: inline-flex;
    height: 45px;
    justify-content: center;
    outline: none;
    width: 45px;

    svg {
      fill: transparent;
      stroke: #a0a0a0;
      stroke-width: 1;
      transform-origin: center;
      transition: fill $comMS $comES, stroke $comMS $comES, transform $comMS $comES;
    }

    &:hover {

      svg {
        transform: scale(1.2);
      }
    }

    &:active,
    &.is-active {

      svg {
        fill: #ed6161;
        stroke: #ed6161;
      }
    }

    p {
      height: 17px;
      width: 19px;
    }

    @include media(sm) {
    }
  }

  // 

  &--add-fav {
    align-items: center;
    background-color: #f8f8f8;
    border: solid 1px #e4e4e4;
    border-radius: 22px;
    color: #a0a0a0;
    display: inline-flex;
    height: 25px;
    justify-content: center;
    width: 140px;

    svg {
      fill: transparent;
      stroke: #a09fa0;
      stroke-width: 1;
      transition: fill $comMS $comES, stroke $comMS $comES;
      width: 13px;
    }

    p {
      @include fz(13, 17, 400);

      margin-left: 10px;
    }

    &:active,
    &.is-active {

      svg {
        fill: #ed6161;
        stroke: #ed6161;
      }
    }

    @include media(sm) {
    }
  }

  // 

  &--blueborder {
    @extend .c-btn;

    background-color: #fff;
    border: solid 2px #4AA2DC;
    color: #4AA2DC;
    @include fz(16, 16, 600);
    height: 35px;
    width: 118px;

    &:hover {
      @include media(lg) {
        color: #fff;
        background-color: #4AA2DC;
      }
    }

    @include media(sm) {
      width: vwset(100);
    }
  }

  // 

  &--simple {
    @extend .c-btn;

    background-color: #fff;
    border: solid 1px #a0a0a0;
    color: #707170;
    @include fz(13, 16, 600);
    height: 35px;
    width: 150px;
    &:hover {
      @include media(lg) {
        background-color: #f8f8f8;
      }
    }
    @include media(sm) {
      width: vwset(230);
    }

    &-large {
      @extend .c-btn;
      background-color: #fff;
      border: solid 1px #a0a0a0;
      color: #707170;
      cursor: pointer;
      @include fz(16, 16, 600);
      height: 45px;
      width: 280px;
        @include media(sm) {
          width: 100%;
      }
    }
  }

  // 

  &--black {
    @extend .c-btn;

    background-color: #393939;
    border: solid 1px #393939;
    color: #fff;
    font-weight: 400;
    height: 35px;
    padding-top: 2px;
    transition: background-color $comMS $comES, border-color $comMS $comES;
    width: 150px;

    @include fz(13, 17, 400);

    &:hover {
      @include media(lg) {
        background-color: #a0a0a0;
        border-color: #a0a0a0;
      }
    }

    @include media(sm) {
      width: vwset(230);

      @include fz(12);
    }
  }

  // 

  &--blue {
    @extend .c-btn;
    cursor: pointer;

    @include fz(16, 16, 600);
    height: 45px;
    width: 280px;

    @include media(sm) {
      width: vwset(230);
    }
    &-w {
      @include media(sm) {
        width: 100%;
      }
    }
  }
  // 

  &--gray {
    @extend .c-btn;

    background-color: #a0a0a0;
    border: solid 1px #a0a0a0;
    color: #fff;
    font-weight: 400;
    height: 45px;
    padding-top: 2px;
    transition: background-color $comMS $comES, border-color $comMS $comES;
    width: 280px;

    @include fz(13, 17, 400);

    &:hover {
      @include media(lg) {
        background-color: #a0a0a0;
        border-color: #a0a0a0;
      }
    }

    @include media(sm) {
      width: vwset(230);

      @include fz(12);
    }
  }


  // 

  &--change {
    @extend .c-btn;

    height: 25px;
    width: 60px;

    @include media(sm) {
      width: vwset(60);
      height: vwset(25);
      @include fz(13, 20, 600);
    }
  }

  &--send {
    @extend .c-btn;

    background-position: right 13px center;
    background-size: 6px auto;
    font-size: 1.6rem;
    font-weight: 600;
    width: 280px;

    @include letterSpacing(50);

    @include media(sm) {
      background-position: right vwset(20) center;
      background-size: vwset(6) auto;
      width: vwset(230);
    }
  }

  &--back {
    color: #2369ba;
    font-size: 1.4rem;
    padding-left: 13px;
    position: relative;

    @include letterSpacing(50);

    &:before {
      border-bottom: 1px solid #2369ba;
      border-right: 1px solid #2369ba;
      content: '';
      display: inline-block;
      height: 7px;
      left: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%) rotate(135deg);
      width: 7px;
    }
  }

  &--address {
    padding: 0 24px;
    border: 2px solid #4aa2dc;
    border-radius: 30px;
    color: #4aa2dc;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    display: inline-flex;
    font-size: 1.4rem;
    height: 35px;
    transition: background-color $comMS $comES, color $comMS $comES;

    @include media(sm) {
      margin-top: vwset(16);
      height: vwset(35);
      padding: 0 vwset(24);
    }

    &:hover {
      transition: background-color $comMS $comES, color $comMS $comES;

      @include media(lg) {
        background-color: #4aa2dc;
        color: #fff;
      }
    }
  }
}
