  // 
  // ----------------------------------------------------------

  &__sender {

    &__navigation {
      align-items: center;
      display: flex;
      justify-content: center;
      margin-top: 80px;
      position: relative;

      .c-textlink--left {
        left: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }

      .c-btn--blue {
        margin: 0 15px;
      }

      @include media(sm) {
        display: block;
        margin-top: vwset(64);
        text-align: center;

        .c-textlink--left {
          left: 0;
          margin-top: vwset(24);
          position: static;
          top: 0;
          transform: translateY(0);
        }

        .c-btn--blue {
          display: flex;
          margin: 0;
          margin-top: vwset(10);
          width: 100%;
        }
      }
    }

    &__list {
      margin-top: 20px;

      @include media(sm) {
        margin-top: vwset(30);
      }
    }
  }

  &__password {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    svg {
      &:nth-of-type(1) {
        width: 16px;
      }
      &:nth-of-type(2) {
        display: none;
        width: 18px;
      }
    }
    &.is-show {
      svg {
        &:nth-of-type(1) {
          display: none;
        }
        &:nth-of-type(2) {
          display: block;
        }
      }
    }
  }

  // 
  // ----------------------------------------------------------

  &__radio-list {
    display: flex;

    li {
      margin-right: 60px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  //  + 
  // ----------------------------------------------------------

  &__tit-radio {
    display: flex;

    @include media(sm) {
      display: block;
    }

    p {
      width: 14%;
      margin-right: 40px;
      @include media(sm) {
        width: 100%;
      }
    }

    &--list {
      width: 86%;
      @include media(sm) {
        width: 100%;
        margin-top: 8px;
      }
      ul {
        display: flex;
        flex-wrap: wrap;
        &:last-of-type{
          margin-top: 8px;
        }

        @include media(sm) {
          flex-wrap: wrap;

          &:last-of-type{
            margin-top: 0px;
          }

          &:first-of-type li{
            margin-top: 0;
          }
        }

        li {
          margin-right: 30px;
          @include media(sm) {
            margin-right: 5%;
            width: 45%;
            margin-top: 8px;
            &:nth-child(2n) {
              margin-right: 0;
            }
          }


          &:last-of-type {
            margin-right: 0;
          }
        }
      }
    }
  }

  //  + 
  // ----------------------------------------------------------

  &__text-select {
    display: flex;
    align-items: center;


    p {
      margin-right: 45px;

      @include media(sm) {
        margin-right: 20px;
      }
    }
  }

  // 
  // ----------------------------------------------------------

  &__card-info {
    background: #f8f8f8;
    border-radius: 5px;
    box-sizing: border-box;
    display: flex;
    margin-top: 20px;
    max-width: 702px;
    padding: 30px;

    @include media(sm) {
      display: block;
      margin: vwset(24) 0 0;
      padding: vwset(24) vwset(20);

      .c-input{
        margin-top: vwset(4);
      }
      .expiration-date__select{
        margin-top: vwset(4);
      }
    }

    //

    .card-number {
      margin-right: 25px;

      @include media(sm) {
        margin-right: 0;
      }

      input:not([type="button"]):not([type="submit"]) {
        width: 240px;

        @include media(sm) {
          width: 100%;
        }
      }

      .c-text--s {
        margin-top: 8px;
      }
    }

    //

    .expiration-date {
      margin: 0 25px 0 0;

      @include media(sm) {
        margin: vwset(16) 0 0 0;
      }

      &__select {
        align-items: center;
        display: flex;

        .c-selectbox-form {
          width: 74px;
          padding: 9px 7px 9px 10px;
          @include media(sm){
            width: 100px;
          }
        }

        p {
          margin: 0 8px;
        }
      }
    }

    //

    .security-code {
      @include media(sm) {
        margin: vwset(16) 0 0 0;
      }
    }
  }

  // 
  // ----------------------------------------------------------

  &__payment-plan {
    display: flex;
    align-items: center;
    margin-top: 25px;

    @include media(sm) {
      display: block;
      margin-top: vwset(24);
    }

    p {
      margin-right: 25px;

      @include media(sm) {
        margin-right: 0;
      }
    }

    .payment-plan__list {
      align-items: center;
      display: flex;

      @include media(sm) {
        display: block;
      }

      li {
        margin-right: 30px;

        @include media(sm) {
          margin-right: 0;
          margin-top: vwset(16);
        }

        &:nth-child(2){
          @include media(sm) {
            display: flex;
            align-items: center;
          }
        }

        .c-selectbox-form {
          margin-left: 5px;
          width: 100px;
          @include media(sm) {
            width: 100%;
          }
        }
      }
    }
  }

  // 
  // ----------------------------------------------------------

  &__card-register {
    display: flex;
    margin-top: 20px;

    @include media(sm) {
      display: block;
      margin-top: vwset(24);
    }

    .c-checkbox {
      @include media(sm) {
        margin-top: vwset(16);
      }
    }

    p {
      margin-right: 45px;

      @include media(sm) {
        margin-right: 0;
      }
    }
  }

  // 
  // ----------------------------------------------------------

  &__pay-easy {

    .c-text--note {
      margin-top: 25px;

      @include media(sm) {
        margin-top: vwset(24);
      }
    }

    .c-textlink {
      @include fz(12, 18);
    }
  }
}

