.l-form__list {
  border-top: 1px solid #e4e4e4;
  display: flex;

  @include media(sm) {
    display: block;
  }

  &--center{
    display: flex;
    align-items: center;
  }

  &--col-2 {
    border-top: 1px solid #e4e4e4;
    display: flex;

    @include media(sm) {
      display: flex;
    }

    .col-2__w55 {
      @include media(sm) {
        min-width: 55%;
        width: 55%;
      }
    }

    .col-2__w45 {
      @include media(sm) {
        min-width: 45%;
        width: 45%;
      }
    }
  }

  &:last-of-type {
    border-bottom: 1px solid #e4e4e4;
  }

  //1

  &--complex {

    .l-form {

      &__complex {
        padding: 0;
      }

      &__complex-block {
        border-bottom: 1px solid #e4e4e4;
        padding: 23px 30px 49px;

        @include media(sm) {
          padding: vwset(24) vwset(12) vwset(27);
        }

        &:last-of-type {
          border-bottom: none;
        }
      }

      &__complex-inner {
        margin-top: 30px;
        padding: 0 28px;

        @include media(sm) {
          margin-top: vwset(24);
          padding: 0 0 0 vwset(25);
        }
      }
    }
  }

  dt {
    align-items: center;
    background-color: #f8f8f8;
    display: flex;
    min-width: 300px;
    .full & {
      min-width: 369px;
      @include media(sm) {
        min-width: 100%;
      }
    }
    padding: 17px 27px;

    @include media(sm) {
      min-width: 100%;
      padding: vwset(17) vwset(16);
      width: 100%;
    }

    &.l-form__top-tit {
      align-items: flex-start;
      display: flex;
      padding-top: 25px;

      @include media(sm) {
        align-items: center;
        padding: vwset(17) vwset(16);
      }
    }
  }

  .c-title--table--reference {
    &::before {
      width: 3rem;
      display: block;
      margin-right: 10px;
      padding: 4px 10px 4px 13px;
      box-sizing: content-box;
      letter-spacing: .3em;
      line-height: 1.5;
      white-space: nowrap;
      content: "";
      @include media(sm) {
        display: none;
      }
    }
  }

  dd {
    padding: 17px 27px;
    width: 100%;
    word-break: break-all;

    @include media(sm) {
      padding: vwset(24) vwset(12);
    }

    //sp
    &.nowrap {
      @include media(sm){
        white-space: nowrap;
      }
    }

    .c-text--xs{
      margin-top: 8px;
    }

    .c-btn--change--large {
      min-width: 150px;
      width: auto;
      padding-left: 10px;
      padding-right: 10px;
    }

    li {
      list-style: none;
    }

    input:not([type="button"]):not([type="submit"]) {
      background: #fff;
      border: 1px solid #707070;
      border-radius: 3px;
      color: #393939;
      outline: none;
      transition: border-color $comMS $comES;
      width: 150px;

      @include letterSpacing(50);

      &:hover,
      &:focus {
        @include media(lg) {
          border-color: #4aa2db;
        }

        &:disabled {
          border: 1px solid #707070;
          opacity: .4;
        }
      }

      &::placeholder {
        color: #a0a0a0;

        &:disabled {
          opacity: .4;
        }
      }

      &:disabled {
        opacity: .4;
      }
    }

    .last-name-wrap {
      display: inline-block;
      margin-right: 25px;

      @include media(sm) {
        align-items: center;
        display: flex;
        justify-content: flex-start;
        margin-right: 0;
        width: 100%;
      }

      input {
        @include media(sm) {
          width: 100%;
        }
      }
    }

    .first-name-wrap {
      display: inline-block;

      @include media(sm) {
        align-items: center;
        display: flex;
        justify-content: flex-start;
        margin-right: 0;
        margin-top: vwset(16);
        width: 100%;
      }

      input {
        @include media(sm) {
          width: 100%;
        }
      }
    }

    .last-name,
    .first-name {
      margin-right: 5px;

      @include media(sm) {
        word-break: keep-all;
      }

      &:nth-of-type(2) {
        margin-left: 26px;
      }
    }

    .price {
      text-align: right;
    }

    .coupon-price p {

      span {
        margin-left: 60px;

        @include media(sm) {
          margin-left: 10px;
        }
      }
    }

    .coupon-code {
      align-items: center;
      display: flex;
      margin-top: 20px;

      @include media(sm) {
        display: block;
        margin-top: vwset(6);

        .c-input{
          margin-top: vwset(4);
          width: 75%;
        }
      }

      p {
        margin-right: 45px;

        @include media(sm) {
          margin-right: 0;
        }
      }

      input {
        margin-right: 10px;
      }
    }
  }

  .list-email {
    display: block;
    width: calc(100% - 369px);

    @include media(sm) {
      width: 100%;
    }

    .email-txt {

      &:last-of-type {
        margin-top: 15px;

        @include media(sm) {
          margin-top: vwset(16);
        }
      }

      p {
        margin: 0 0 3px;
      }

      input:not([type="button"]):not([type="submit"]) {
        width: 600px;

        @include media(sm) {
          width: 100%;
        }
      }
    }
  }

  .pref-wrap {
    display: block;

    @include media(sm) {
      align-items: center;
      display: flex;
      justify-content: flex-start;
      width: 100%;
    }
    &--inline {
      display: inline-block;
      margin-right: 15px;
    }

    .c-selectbox {
      width: 200px;
      @include media(sm) {
        width: 100%;
      }
    }
  }

  .birth-year-wrap {
    display: inline-block;
    margin-right: 5px;

    .c-selectbox {
      width: 100px;
      @include media(sm) {
        width: auto;
        margin-right: 5px;
      }
    }
    .c-select__wrap {
      &:before {
        @include media(sm){
          right: 12px;
        }
      }
    }
    .c-select--small-birth {
      @include media(sm){
        width: vwset(80) !important;
      }
    }
  }

  .birth-month-wrap {
    display: inline-block;
    margin-left: 5px;

    .c-selectbox {
      width: 70px;
      @include media(sm) {
        width: auto;
        margin-right: 5px;
      }
    }
  }

  .birth-day-wrap {
    display: inline-block;
    margin-left: 5px;

    .c-selectbox {
      width: 70px;
      @include media(sm) {
        width: auto;
      }
    }
  }

  .birth-year,
  .birth-month,
  .birth-day {
    margin-left: 5px;

    @include media(sm) {
      word-break: keep-all;
    }
  }

  .sex-wrap {
    display: inline-block;
    margin-right: 5px;
    &:nth-of-type(n + 2) {
      margin-left: 40px;
    }

    @include media(sm) {
      &:nth-of-type(n + 2) {
        margin-left: 20px;
      }
    }
  }

  .category-wrap {
    display: inline-block;
    margin-right: 5px;
    &:nth-of-type(n + 2) {
      margin-left: 40px;
    }

    @include media(sm) {
      display: block;
      &:nth-of-type(n + 2) {
        margin: 10px 0 0;
      }
    }
  }

  .tel-txt {

    input:not([type="button"]):not([type="submit"]) {
      display: block;
      width: 190px;
    }

    span {
      display: block;
    }
  }

  .post-txt {
    .c-btn--address{
      @include media(sm) {
        margin-top: 0;
      }
    }


    input:not([type="button"]):not([type="submit"]) {
      width: 70px;

      @include media(sm) {
        width: 20%;
      }

      &:last-of-type {
        margin-right: 15px;
        width: 90px;

        @include media(sm) {
          width: 23%;
        }
      }
    }

    span {
      margin: 0 5px;
    }
  }

  .ward-txt {

    input:not([type="button"]):not([type="submit"]) {
      width: 600px;

      @include media(sm) {
        width: 100%;
      }
    }
  }

  .password-wrap {
    width: 400px;
    position: relative;

    input:not([type="button"]):not([type="submit"]) {
      width: 100%;
      padding-right: 40px;
    }
    button {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      svg {
        fill: #a0a0a0 !important;
      }
    }

    @include media(sm) {
      width: 100%;
    }
  }
  .c-text--s + .password-wrap {
    margin-top: 20px;

    @include media(sm) {
      margin-top: vwset(10);
    }
  }

  .email-double {
    input:not([type="button"]):not([type="submit"]) {
      width: 400px;
      @include media(sm) {
        width: 100%;
      }
    }
    p {
      margin: 18px 0 8px;
    }
  }
}

.l-form__send {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-top: 60px;

  &:after {
    content: "";
    display: block;
    width: 130px;
  }
}
