@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.guide-order {

  .anchor {
    a:first-child {
      margin-right: 40px;
      @include media(sm) {
        margin-right: vwset(40);
      }
    }
  }

  .return {
    li {
      list-style: none;
    }
  }

  .step {

    &__area {
      width: 100%;
      display: flex;
      @include media(sm) {
        flex-direction: column;
      }
    }
    &__desc {
      width: 500px;
      @include media(sm) {
        width: 100%;
      }
    }
    &__text {
      span {
        color: #4AA2DB;
        font-weight: bold;
      }
      &--caution {
        padding-left:1em;
        text-indent:-1em;
      }
    }
    &__image {
      width: 405px;
      margin-left: 50px;
      @include media(sm) {
        width: 100%;
        margin-top: vwset(10);
        margin-left: 0;
      }
      img {
        @include media(sm) {
          width: 100%;
          height: auto;
        }
      }
    }
  }

}
