@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;


.c-label-status {
  width: 100px;
  height: 30px;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include media(sm) {
    width: vwset(100);
    height: vwset(30);
  }
  &--blue {
    color: #2369BA;
    border: solid 1px #2369BA;
  }
  &--orange {
    color: #E07409;
    border: solid 1px #E07409;
  }
  &--red {
    color: #E31616;
    border: solid 1px #E31616;
  }
  &--black {
    color: #393939;
    border: solid 1px #393939;
  }
  &--green {
    color: #51ba23;
    border: solid 1px #51ba23;
  }
}
