
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

//    (PC)
.c-product-info__pc {

    // 
  &--order {
    display: flex;
    @include media(sm) {
      display:  none;
    }

    tr:not(:last-of-type):not(:first-of-type) {
      border-bottom: solid 1px #e4e4e4;
    }

    th {
      background: #f8f8f8;
      padding: 10px;
      text-align: center;
    }

    td {
      .tax {
          margin-left: 5px;
          color: #a0a0a0;
      }
    }

    &__list {
      padding: 20px 20px 20px 0;

      @include media(sm) {
        padding: 20px 0;
      }
    }

    &__info {
      border-right: solid 2px #fff;

      &--name {
        width: 502px;
      }

      &--price {
        width: 239px;
        &-2col {
          width: 180px;
        }
      }

      &--number {
        width: 214px;
        &-2col {
          width: 92px;
        }
      }

      &--subtotal {
        width: 239px;
        &-2col {
          width: 180px;
        }
      }
    }

    &__desc {
      display: flex;

      @include media(sm) {
        flex-direction: column;
      }

      > div {
                overflow: hidden;
      }

      a {
        width: 120px;
        min-width: 120px;
        @include object-fit(cover, center);
        transform-origin: center;
        transition: opacity $comMS $comES, transform $comMS $comES;
        @include media(sm) {
          display: flex;
          justify-content: center;
          padding: 0 10px 10px;
        }
        &:hover {
          @include media(lg) {
            img {
              opacity: .5;
              transform: scale(1.5);
            }
          }
        }
      }

      img {
        width: 120px;
        height: 120px;
        @include object-fit(cover, center);
        transform-origin: center;
        transition: opacity $comMS $comES, transform $comMS $comES;
      }

      ul {
        align-items: flex-start;
        display: flex;
        flex-direction: column;
        justify-content: center;
        list-style: none;
        margin-left: 20px;

        @include media(sm) {
          margin: 0;
        }
      }
    }

    &__list {

      &--right {
        text-align: right;
      }

      &--center {
        padding: 20px 0;
        text-align: center;
      }
    }
  }


  //
  &--purchase {
      display: flex;
      @include media(sm) {
        display: none;
      }
  
      tr:not(:first-of-type) {
        border-bottom: solid 1px #e4e4e4;
      }
  
      th {
        background: #f8f8f8;
        padding: 10px;
        text-align: center;
        @include media(sm) {
          display: none;
        }
      }
  
      &__list {
        padding: 20px 0;
  
        @include media(sm) {
          padding: 20px 0;
        }
      }
  
      &__info {
        border-right: solid 2px #fff;
  
        &--name {
          width: 840px;
  
          @include media(sm) {
            width: 60%;
          }
        }
  
        &--share {
          width: 360px;
  
          @include media(sm) {
            width: 40%;
          }
        }
      }
  
      &__desc {
        display: flex;
  
        @include media(sm) {
          flex-direction: column;
        }
  
        > div {
                  overflow: hidden;
        }
  
        a {
          width: 120px;
          min-width: 120px;
          @include object-fit(cover, center);
          transform-origin: center;
          transition: opacity $comMS $comES, transform $comMS $comES;
          @include media(sm) {
            display: flex;
            justify-content: center;
            padding: 0 10px 10px;
          }
          &:hover {
            @include media(lg) {
              img {
                opacity: .5;
                transform: scale(1.5);
              }
            }
          }
        }
  
        img {
          width: 120px;
          height: 120px;
          @include object-fit(cover, center);
          transform-origin: center;
          transition: opacity $comMS $comES, transform $comMS $comES;
        }
  
        ul {
          align-items: flex-start;
          display: flex;
          flex-direction: column;
          justify-content: center;
          list-style: none;
          margin-left: 20px;
  
          @include media(sm) {
            margin: 0;
          }
        }
      }
  
      &__list {
  
        &--right {
          text-align: right;
        }
  
        &--center {
          padding: 20px 0;
          text-align: center;
        }
      }
  
      //SNS
  
      .share {
  
        &__list {
          display: flex;
          justify-content: center;
          list-style: none;
          margin: 0 auto;
          max-width: 260px;
          width: 100%;
  
          @include media(sm) {
            display: flex;
          }
        }
  
        &__item {
          margin-right: 30px;
          width: 80px;

          > a {
            display: flex;
            margin: 0 auto;
            transition: opacity $comMS $comES;
  
            @include media(sm) {
              width: 40px;
            }
  
            &:hover {
              opacity: .5;
              transition: opacity $comMS $comES;
            }
          }
  
          @include media(sm) {
            align-items: center;
            display: flex;
            flex-direction: column;
            margin: 0 auto 15px;
            width: 50%;
          }
  
          &:last-of-type {
            margin: 0;
  
            @include media(sm) {
              margin: 0 auto;
            }
          }
  
          &-name {
            margin-top: 10px;
            text-align: center;
            font-family: $fontFamilyEn;
            color: #A0A0A0;
  
            @include media(sm) {
              margin-top: 5px;
            }
          }
        }

        &__icon {
          width: 60px;
          height: 60px;
          padding: 15px;
          border-radius: 5px;
          display: flex;
          justify-content: center;
          align-items: center;
            > svg {
            width: 30px;
            height: 30px;
            @include media(sm) {
              width: vwset(30);
              height: vwset(30);
            }
          }
          &--line {
            background: #00B900;
          }
          &--twitter {
            background: #1D9BF0;
          }
          &--facebook {
            background:#1877F2;
          }
        }

        
      }
    }
  }
