@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.l-hmb {
  background-color: #fff;
  height: vwset(70);
  order: 1;
  position: relative;
  width: vwset(70);

  span {
    background-color: #393939;
    border-radius: vwset(4);
    height: vwset(2);
    left: vwset(23);
    position: absolute;
    width: vwset(24);

    &:nth-of-type(1) {
      top: calc(50% - #{vwset(8)});
    }

    &:nth-of-type(2) {
      top: calc(50% - #{vwset(1)});
    }

    &:nth-of-type(3) {
      top: calc(50% + #{vwset(6)});
    }
  }

  &-close {
    height: vwset(60);
    position: absolute;
    right: 0;
    top: 0;
    width: vwset(60);

    span {
      background-color: #fff;
      border-radius: vwset(4);
      height: vwset(2);
      left: vwset(18);
      position: absolute;
      transform-origin: center;
      width: vwset(24);

      &:nth-of-type(1) {
        top: calc(50% - #{vwset(2)} / 2);
        transform: rotate(-315deg);
      }

      &:nth-of-type(2) {
        bottom: calc(50% - #{vwset(2)} / 2);
        transform: rotate(315deg);
      }
    }
  }

  &-bg {
    background-color: rgba(#000, .5);
    height: 100%;
    left: 0;
    opacity: 0;
    position: fixed;
    top: 0;
    transition: $comMS $comES;
    visibility: hidden;
    width: 100%;
    will-change: opacity;
    z-index: 800;

    .is-nav-active & {
      opacity: 1;
      visibility: visible;
    }
  }
}
