@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.c-price-list {

    dl {
      border-top: 1px solid #e4e4e4;
      display: flex;
      font-size: 1.4rem;
      width: 100%;

      &:last-of-type {
        border-bottom: 1px solid #e4e4e4;
      }
    }

    dt {
      background: #f8f8f8;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 13px 20px;
      width: 960px;
      @include media(sm) {
        text-align: right;
        width: 50%;
        white-space: pre;
      }
    }

    dd {
      align-items: center;
      display: flex;
      justify-content: flex-end;
      padding: 0 20px;
      width: 240px;

      @include media(sm) {
        padding: 0 10px;
        width: 50%;
      }
    }

    &__wrap {
      display: flex;
    }

    &__inner-wrap {
      padding: 15px 0;

      @include media(sm) {
        width: 50%;
      }

      > dd {
        @include media(sm) {
          width: 100%;
        }
      }
    }

    &__item {
      align-items: flex-end;
      color: #a0a0a0;
      display: flex;
      justify-content: flex-end;

      &--left {
        max-width: 120px;
        width: 100%;
        @include media(sm) {
          max-width: initial;
          width: 62%;
        }
      }

      &--right {
        max-width: 140px;
        width: 100%;
        @include media(sm) {
          width: 38%;
        }
      }
    }

    &__total {
      color: #e31616;
      padding: 30px 0;
      @include media(sm) {
        padding: 0;
      }
    }

  }
