
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

%list {
  list-style: none;
}

%list-item {
  padding-left: 1em;
  position: relative;

  &:before {
    left: 0;
    position: absolute;
    top: 0;
  }
}

.c-list {

  &--note {
    @extend %list;

    .item {
      @extend %list-item;

      &:before {
        content: "";
      }
    }
  }

  &--dot {
    @extend %list;

    .item {
      @extend %list-item;

      &:before {
        content: "";
      }
    }
  }

  &--definition {

    &__title {
      color: #4aa2db;
    }

    &__content {
      margin-top: 1em;

      + .c-list--definition__title {
        margin-top: 2em;
      }
    }
  }
}
