@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.news-detail {

  &__image {
    img {
      width: 100%;
    }
    figcaption {
      text-align: center;
    }
  }

  &__block {
    &--l {
      @extend .news-detail__block;
    }
    &--m {
      @extend .news-detail__block;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .news-detail__image {
        width: calc((100% - 20px) / 2);
      }
    }
    &--s {
      @extend .news-detail__block;
      display: flex;
      align-items: flex-start;
      .news-detail__image {
        width: calc((100% - 40px) / 3);
        &:nth-of-type(n + 2) {
          margin-left: 20px;
        }
      }
    }

    @include media(sm) {
      &--m {
        display: block;
        .news-detail__image {
          width: auto;
          &:nth-of-type(n + 2) {
            margin-top: vwset(20);
          }
        }
      }
      &--s {
        display: block;
        .news-detail__image {
          width: auto;
          &:nth-of-type(n + 2) {
            margin: vwset(20) 0 0;
          }
        }
      }
    }
  }
}
