@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.l-footer {
  background-color: #393939;
  color: #fff;
  //margin-top: 120px;
  margin-top: auto;

  &__inner {
    @include width(900px, 20px, vwset(24));

    padding: 30px 0 40px;
  }

  &__logo {
    display: block;
    margin: 0 auto;
    width: 155px;

    img {
      width: 100%;
    }
  }

  &__nav {
    margin-top: 30px;

    ul {
      align-items: center;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      list-style: none;
    }

    a {
      align-items: center;
      color: #fff;
      display: flex;
      text-decoration: none;
      transition: color $comMS $comES;

      svg {
        transition: fill $comMS $comES;
        width: 4px;
      }

      &:hover {
        @include media(lg) {
          color: #60a1d6;

          svg {
            fill: #60a1d6 !important;
          }
        }
      }

      span {
        align-items: center;
        display: flex;
        height: 8px;
        justify-content: center;
        width: 4px;
      }

      p {
        @include fz(12, 18, 600);

        margin-left: 7px;
      }
    }
  }

  &__copyright {
    background-color: #000;
    color: #fff;
    display: block;
    padding: 20px 0;
    text-align: center;

    @include fz(13, 18, 600);
  }

  @include media(sm) {
    //margin-top: vwset(60);

    &__inner {
      padding: vwset(25) 0 vwset(30);
    }

    &__logo {
      width: vwset(155);
    }

    &__nav {
      flex-wrap: wrap;
      margin-top: 30px;

      ul {
        justify-content: flex-start;

        li {
          width: 50%;

          &:nth-of-type(n + 3) {
            margin-top: vwset(10);
          }
        }
      }

      a {
        @include letterSpacing(50);

        svg {
          width: vwset(4);
        }

        span {
          height: vwset(8);
          min-width: vwset(4);
          width: vwset(4);
        }

        p {
          @include fz(12, 20, 600);

          margin-left: vwset(5);
        }
      }
    }

    &__copyright {
      padding: vwset(20) 0;
    }
  }
}

// 

.pagetop {
  align-items: center;
  background-color: #fff;
  border-radius: 50%;
  bottom: 60px;
  box-shadow: 0 3px 6px rgba(#929eb1, .5);
  display: flex;
  height: 39px;
  justify-content: center;
  opacity: 0;
  position: fixed;
  right: 20px;
  transform-origin: center;
  transition: transform $comMS $comES, opacity $comMS $comES, visibility $comMS $comES;
  visibility: hidden;
  width: 39px;
  z-index: 100;

  svg {
    width: 18px;
  }

  &:hover {
    @include media(lg) {
      transform: scale(1.36);
    }
  }

  .is-scroll & {
    opacity: 1;
    visibility: visible;
  }

  @include media(sm) {
    bottom: vwset(60);
    box-shadow: 0 vwset(3) vwset(6) rgba(#929eb1, .5);
    height: vwset(39);
    right: vwset(12);
    width: vwset(39);
  }
}
