@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.card-index {
  .c-confirm-list {
    dl {
      &:last-of-type {
        .c-confirm-list__detail {
          input {
            @include media(sm){
              width: 100%;
            }
          }
          .expiration-date__select {
            display: flex;
            align-items: center;
            p {
              margin: 0 8px;
            }
            .c-selectbox-form__wrap {
              .c-selectbox-form {
                margin-right: 0;
                width: 76px;
                @include media(sm){
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }
  .c-confirm-list__detail {
    display: block;
    input {
      width: 400px;
      @include media(sm){
        width: 100%;
      }
    }
  }
  .l-form__sender__navigation {
    @include media(sm){
      width: 100%;
      flex-direction: column-reverse;
    }
  }
  .birth-month-wrap {
    .c-selectbox {
      width: 70px;

    }
  }
  .c-form {
    nav {
      .c-btn--blue {
        @include media(sm){
          margin: 0;
        }
      }
      .c-btn--simple {
        @include media(sm){
          margin: 4vw 0 0 !important;
        }
      }
    }
  }
}
