@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.top {

  .l-container {
    margin-top: 50px;

    @include media(sm) {
      margin-top: vwset(40);
    }
  }

  .l-main {
    margin-left: 0;
    margin-right: 0;

    @include media(sm) {
      margin-left: 0;
      margin-right: 0;
      width: 100%;
    }
  }

  // MV

  &__mv {
    overflow-x: hidden;
    padding-bottom: 30px;
    position: relative;
    width: 100%;

    .swiper-slide {
      width: 980px;
    }

    .swiper-button-prev,
    .swiper-button-next {
      background-color: rgba(#fff, .6);
      background-position: center;
      background-size: 11px auto;
      height: 45px;
      top: calc(50% - 15px);
      width: 45px;

      &:after {
        content: none;
      }
    }

    .swiper-button-prev {
      @include icon('arrow-left', '393939');

      left: 0;
    }

    .swiper-button-next {
      @include icon('arrow-right', '393939');

      right: 0;
    }

    .swiper-pagination {
      bottom: 0 !important;
      display: flex;
      justify-content: center;

      .swiper-pagination-bullet {
        background-color: #e4e4e4;
        height: 10px;
        margin: 0 9px !important;
        opacity: 1;
        width: 10px;
      }

      .swiper-pagination-bullet-active {
        background-color: #4aa2db !important;
      }
    }

    @include media(sm) {
      padding-bottom: vwset(40);

      .swiper-slide {
        width: 100vw;
      }

      .swiper-button-prev,
      .swiper-button-next {
        display: none;
      }

      .swiper-pagination {
        align-items: center;
        background-color: #f8f8f8;
        height: vwset(40);
        justify-content: center;

        .swiper-pagination-bullet {
          height: vwset(8);
          margin: 0 vwset(8) !important;
          width: vwset(8);
        }
      }
    }
  }

  // 

  &__title {

    &--content {

      strong {
        display: block;

        @include fz(24, 28, 600);
      }

      small {
        color: #a0a0a0;
        display: block;
        font-family: $fontFamilyEn;
        margin-top: 10px;

        @include fz(14, 19, 600);
        @include letterSpacing(100);
      }

      @include media(sm) {

        strong {
          @include fz(20, 24, 600);
        }

        small {
          margin-top: vwset(10);
        }
      }
    }
  }

  // 

  hr {
    border: none;
    border-bottom: solid vwset(30) #e4e4e4;
    margin-top: vwset(60);
  }

  // 

  &__new {

    .c-product-list--thumb {
      margin-top: 30px;
    }

    @include media(sm) {
      margin: 0 vwset(12);

      .c-product-list--thumb {
        margin-top: vwset(30);
      }
    }
  }

  &__popular {
    margin-top: 110px;

    .c-product-list--ranking {
      margin-top: 30px;

      @include media(sm) {
        margin-top: vwset(30);
      }

      .swiper-pagination {
        @include media(lg) {
          display: none;
        }
      }
    }

    @include media(sm) {
      margin-top: vwset(40);

      .top__title--content {
        margin: 0 vwset(12);
      }

      .c-product-list--thumb {
        margin-top: vwset(30);
      }
    }
  }

  &__reccomend {
    margin-top: 100px;

    .c-product-list--thumb {
      margin-top: 30px;
    }

    @include media(sm) {
      margin: vwset(40) vwset(12) 0;

      .c-product-list--thumb {
        margin-top: vwset(30);
      }
    }
  }

  &__category {
    margin-top: 120px;

    .c-title--line {
      @include media(sm) {
        margin-left: vwset(12);
        margin-right: vwset(12);
      }
    }

    .c-product-list--ranking {
      margin-top: 20px;

      .swiper-pagination {
        @include media(lg) {
          display: none;
        }
      }
    }

    section {
      margin-top: 40px;

      &:nth-of-type(n + 2) {
        margin-top: 60px;
      }

      nav {
        margin-top: 40px;
        text-align: right;
      }
    }

    @include media(sm) {
      margin-top: vwset(40);

      .top__title--content {
        margin: 0 vwset(12);
      }

      .top__title--line {
        margin: 0 vwset(24);
      }

      section {
        margin-top: vwset(40);

        &:nth-of-type(n + 2) {
          margin-top: vwset(60);
        }

        .c-product-list--ranking {
          margin-top: vwset(12);
        }

        nav {
          margin-top: vwset(30);
          text-align: center;
        }
      }
    }
  }

  // 

  &__news {
    margin-top: 90px;

    ul {
      list-style: none;

      li {
        border-bottom: solid 1px #e4e4e4;

        a, .readonly-area {
          align-items: flex-start;
          display: flex;
          padding: 20px 20px 20px 0;
          text-decoration: none;
          transition: color $comMS $comES;

          &:not(.readonly-area){
            &:hover {
              @include media(lg) {
                color: #2369ba;
              }
            }
          }

          time {
            @include fz(15, 20);

            white-space: nowrap;
          }

          /*p {
            @include fz(13, 20);

            margin: 0 20px;
          }*/

          span {
            background-position: right center;
            background-size: 4px auto;
            color: #2369ba;
            margin-left: auto;
            min-width: 35px;
            white-space: nowrap;
            width: 35px;

            @include icon('arrow-right', '2369ba');
            @include fz(12, 20);
          }
        }
      }
    }

    @include media(sm) {
      margin: vwset(40) vwset(12) 0;

      ul {
        list-style: none;

        li {
          border-bottom: solid 1px #e4e4e4;

          a, .readonly-area {
            flex-wrap: wrap;
            padding: vwset(20) vwset(45) vwset(20) 0;
            position: relative;

            time {
              width: 100%;
            }

            /*p {
              flex-grow: 0;
              margin: vwset(10) 0 0;
            }*/

            span {
              background-size: vwset(4) auto;
              margin-left: 0;
              min-width: initial;
              position: absolute;
              right: 0;
              top: vwset(50);
              width: vwset(35);

              @include fz(13, 20, 600);
            }
          }
        }
      }
    }

    &__text-area {
      margin: 0 20px;

      @include media(sm) {
        flex-grow: 0;
        margin: vwset(10) 0 0;
      }
    }

    &__txt {
      @include fz(14, 19);
    }

    &__con {
      @include fz(12, 18);

      margin-top: 10px;

      @include media(sm) {
        margin-top: vwset(8);
      }
    }
  }
}
