@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/icon" as *;

.c-textlink {
  color: #2369ba;
  display: inline-flex;

  @include fz(14, 24, 400);
  @include letterSpacing(50);

    //
    &--large {
      @extend .c-textlink;
      @include fz(16, 27, 600);
    }

    //
    &--small {
      @extend .c-textlink;
      @include fz(13, 20, 400);
    }

  p {
    position: relative;

    &:after {
      background-color: #2369ba;
      bottom: 0;
      content: '';
      height: 1px;
      left: 0;
      opacity: 0;
      position: absolute;
      width: 100%;
    }
  }

  &:hover {

    p {

      &:after {
        opacity: 1;
      }
    }
  }

  // 

  &--right {
    @extend .c-textlink;

    @include icon('arrow-right', '2369ba');

    background-position: right center;
    background-size: 6px auto;
    padding-right: 20px;
  }

  // 

  &--left {
    @extend .c-textlink;

    @include icon('arrow-left', '2369ba');

    background-position: left center;
    background-size: 6px auto;
    padding-left: 14px;
  }

    // 

    &--bottom {
      @extend .c-textlink;
  
      @include icon('arrow-down', '2369ba');
  
      background-position: right center;
      background-size: 10px auto;
      padding-right: 20px;
    }

  // 

  &--delete {
    @extend .c-textlink;

    &:after {
      content: '';
      margin-left: 3px;
    }
  }

  // 

  &--add {
    @extend .c-textlink;

    &:before {
      content: '';
      margin-right: 3px;
    }
  }

  // 

  &--underline {
    color: #4aa2db;
    text-decoration: underline;
    transition: opacity $comMS $comES;

    &:hover {
      color: #2369ba;
    }
  }

  //

  &--large {
    @extend .c-textlink;

    @include fz(16, 27, 600);
  }
}
