@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-text {
  //font-size 12px (XD: )
  &--xs {
    @include fz(12);
    @include letterSpacing(100);
    line-height: 1.8;
  }

  //font-size 13px (XD: 13pt)
  &--s {
    @include fz(13);
    @include letterSpacing(100);
    line-height: 1.8;
  }

  //font-size 14px (XD:)
  &--m {
    @include fz(14);
    @include letterSpacing(100);
    line-height: 1.8;
  }

  //font-size 16px
  &--l {
    @include fz(16);
    @include letterSpacing(100);
    line-height: 1.8;
  }

  //PC font-size1.4rem / SP font-size1.2rem
  &--m-center{
    text-align: center;
    @include fz(14);
    @include letterSpacing(100);
    line-height: 1.8;
    @include media(sm) {
      text-align: left;
      @include fz(12);
    }
  }

  //PC font-size1.6rem / SP font-size1.6rem
  &--l-center{
    text-align: center;
    @include fz(16);
    @include letterSpacing(100);
    line-height: 1.8;
    @include media(sm) {
      text-align: left;
      @include fz(16);
    }
  }


  //  
  &--note {
    color: #e31616;
    @include fz(14);
    @include letterSpacing(100);
    line-height: 1.8;
  }

  //  &

  &--caution {
    background-color: #f8f8f8;
    padding: 25px 20px;

    strong {
      color: #e31616;
      display: block;
      @include fz(14,26 ,300);
      line-height: 1.8;
      @include letterSpacing(100);
    }

    p {
      @include fz(12);
      @include letterSpacing(100);
      line-height: 1.8;
      margin-top: 13px;
    }

    @include media(sm) {
      padding: vwset(25) vwset(20);

      strong {
        @include fz(14, 26, 300);
      }

      p {
        @include fz(12);
        line-height: 1.8;
        @include letterSpacing(100);
        margin-top: vwset(13);
      }
    }
  }

  //
  &--nolist {
    @include fz(20);
    line-height: 1.8;
    color: #2369ba;
  }


}
