
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.order-complete {

  //  

  .announce {

    &__text {
      text-align: center;
      @include media(sm) {
        text-align: left;
      }
    }

    .order-number {
      border: solid 1px #e4e4e4;
      display: flex;
      margin: 35px auto 0;
      max-width: 610px;
      width: 100%;
      @include media(sm) {
        flex-direction: column;
      }

      .c-textlink--large {
        margin-right: 20px;
        vertical-align: middle;
        @include media(sm) {
          margin-right: 0px;
        }
      }      
      .c-text--s {
        vertical-align: middle;
      }

      &__text  {
        text-align: center;

        > a {
          position: relative;
        }

        > a:hover {
          &:after {
            background-color: #2369ba;
            bottom: 0;
            content: '';
            height: 1px;
            left: 0;
            opacity: 1;
            position: absolute;
            width: 100%;
          }
        }
        
      }

      > div {
        padding: 20px;
        @include media(sm) {
          padding: 17px 20px;
        }
      }

      > div:first-child {
        background: #f8f8f8;
        display: flex;
        align-items: center;
        justify-content: center;
        @include media(sm) {
          width: 100%;
        }
      }
    }
  }

  .contact-link {
    background: #f8f8f8;
    border-radius: 5px;
    padding: 35px 85px;
    text-align: center;
    width: 100%;

    @include media(sm) {
      padding: 20px;
    }

    &__title {
      margin-top: 15px;
    }

    .link-area {
      display: flex;
      margin-top: 15px;

      @include media(sm) {
        flex-direction: column;
      }
      > a {
        width: 100%;
        height: auto;
        display: block;
        &:hover {
          .c-textlink {
            position: relative;
            &:after {
              background-color: #2369ba;
              bottom: 0;
              content: '';
              height: 1px;
              left: 0;
              opacity: 1;
              position: absolute;
              width: 100%;
            }
          }
        }
        > div {
          max-width: 500px;
          width: 100%;
          background: #fff;
          border-radius: 5px;
          padding: 25px 0;
          @include media(sm) {
            padding: 20px;
            margin: 0 auto;
          }
        }
      }

      > a:first-child {
        margin-right: 30px;
        @include media(sm) {
          margin: 0 auto 15px;
        }
      }

      &__text {
        margin-bottom: 5px;
      }
    }
  }

  .c-btn--blue {
    @include fz(16, 21, 600);
  }

}
