@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.c-step {
  @include fz(13, 18, 400);

  @include media(sm) {
    @include fz(12);

    display: none;
  }

  &__list {
    align-items: center;
    display: flex;
    flex-wrap: wrap;

    svg {
      margin: 0 20px;
      width: 6px;
    }

    @include media(sm) {

      svg {
        margin: vwset(14) vwset(10) 0;
        width: vwset(4);
      }
    }
  }

  &__item {
    color: #a0a0a0;
    display: inline-block;
    position: relative;

    &:after {
      background-color: #60a1d6;
      bottom: -5px;
      content: '';
      height: 2px;
      left: 0;
      opacity: 0;
      position: absolute;
      transition: opacity $comMS $comES;
      width: 100%;
    }

    &.is-finished {
      color: #393939;

      &:after {
        opacity: 1;
      }
    }

    @include media(sm) {
      margin-top: vwset(14);
    }
  }
}
