@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.l-side {
  width: 190px;

  ul {
    list-style: none;
  }

  // 

  &__banner {
    margin-top: 30px;

    a {
      display: block;
      transition: opacity $comMS $comES;

      &:hover {
        opacity: .5;
      }

      img {
        width: 100%;
      }
    }
  }

  // 

  &-cat {

    strong {
      align-items: center;
      border-bottom: solid 1px #e4e4e4;
      display: flex;
      padding-bottom: 20px;

      svg {
        height: 19px;
        width: 19px;
      }

      span {
        display: block;
        margin-left: 7px;

        @include fz(20, 27, 600);
      }
    }

    a {
      transition: color $comMS $comES;

      svg {
        transition: fill $comMS $comES;
      }

      &:hover {
        color: #2369ba;

        svg {
          fill: #2369ba !important;
        }
      }
    }

    &__parent {
      &.mypage {
        & > li {
          margin-top: 0;
          padding: 25px 8px;
          &:first-of-type {
            border-top: solid 1px #e4e4e4;
          }
          & > a {
            span {
              width: 14px;
              height: 14px;
            }
            p {
              @include fz(14, 19, 300);
              letter-spacing: 0;
            }
          }
        }
      }
      &.guide {
        & > li {
          margin-top: 0;
          padding: 25px 8px;
          &:first-of-type {
            border-top: solid 1px #e4e4e4;
          }
          & > a {
            align-items: flex-start;
            span {
              width: 14px;
              height: 14px;
              padding-top: 4px;

              svg {
                width: 14px;
              }
            }
            p {
              @include fz(14, 21, 300);
              //letter-spacing: 0;
            }
          }
        }
      }

      & > li {
        border-bottom: solid 1px #e4e4e4;
        margin-top: 25px;
        padding-bottom: 25px;

        > a{
          &.is-current {
            color: #2369ba;

            svg {
              fill: #2369ba !important;
            }
          }
        }

        &.is-current {
          background-color: #f8f8f8;
          a {
            color: #2369ba;

            svg {
              fill: #2369ba !important;
            }
          }
        }



        > a {
          align-items: center;
          display: flex;

          span {
            align-items: center;
            display: flex;
            height: 20px;
            justify-content: center;
            width: 20px;
          }
          p {
            @include fz(16, 21, 600);

            margin-left: 10px;
          }

          svg {

            &.categoryIcon {
              width: 19px;
            }

            &.mypage01 {
              width: 14px;
            }
            &.mypage02 {
              width: 14px;
            }
            &.mypage03 {
              width: 14px;
            }
            &.mypage04 {
              width: 14px;
            }
            &.mypage05 {
              width: 14px;
            }
            &.mypage06 {
              width: 14px;
            }
            &.mypage07 {
              width: 14px;
            }
            &.mypage08 {
              width: 14px;
            }
            &.mypage09 {
              width: 14px;
            }
            &.mypage10 {
              width: 14px;
            }
          }
        }
      }
    }

    &__child {
      margin: 14px 0 0;

      >li {

        &:nth-of-type(n + 2) {
          margin-top: 7px;
        }

        >a {
          align-items: center;
          display: flex;
          padding: 6px 0 6px 27px;

          span {
            align-items: center;
            display: flex;
            height: 8px;
            justify-content: center;
            width: 5px;
          }

          p {
            @include fz(13, 20);
            @include letterSpacing(100);
            margin-left: 10px;
          }
        }
      }

      li {
        a {
          &.is-current {
            background-color: #f8f8f8;
            color: #2369ba;

            svg {
              fill: #2369ba !important;
            }
          }
        }
      }
    }

    &__grandchild {
      margin: 10px 0 0;

      & > li {

        &:nth-of-type(n + 2) {
          margin-top: 7px;
        }

        & > a {
          display: block;
          padding: 6px 0 6px 43px;

          p {
            @include fz(13, 20);
            @include letterSpacing(100);
          }
        }
      }
    }
  }

  // 

  &-sch {

    strong {
      align-items: center;
      border-bottom: solid 1px #e4e4e4;
      display: flex;
      padding-bottom: 20px;

      svg {
        height: 16px;
        width: 16px;
      }

      span {
        display: block;
        margin-left: 10px;
        @include fz(20, 27, 600);
      }
    }

    &__block {
      border-bottom: solid 1px #e4e4e4;
      margin-top: 30px;
      padding-bottom: 30px;

      &--title {
        @include fz(16, 21, 600);

        padding-bottom: 10px;
      }

      div {
        align-items: center;
        display: flex;

        &:nth-of-type(n + 2) {
          margin-top: 10px;
        }

        span {
          @include fz(14, 19, 600);

          margin-right: 10px;
          white-space: nowrap;
        }
      }
    }

    .c-input {
      width: 100%;
    }

    .c-selectbox {
      display: block;
    }

    nav {
      margin-top: 30px;

      .c-btn--search {
        width: 100%;
      }

      .c-btn--simple {
        margin-top: 10px;
        width: 100%;
      }
    }
  }

    // 

    &-member {

      strong {
        align-items: center;
        border-bottom: solid 1px #e4e4e4;
        display: flex;
        padding-bottom: 20px;

        svg {
          height: 19px;
          width: 19px;
        }

        span {
          display: block;
          margin-left: 7px;

          @include fz(20, 27, 600);
        }
      }

      a {
        padding: 20px 10px;
        transition: background $comMS $comES;
        transition: color $comMS $comES;

        @include notMacosIos{
          padding: 20px 0 20px 10px;
        }

        svg {
          transition: fill $comMS $comES;
        }

        &:hover {
          color: #2369ba;
          background: #F8F8F8;

          svg {
            fill: #2369ba !important;
          }
        }
      }

      &__parent {

        & > li {
          border-bottom: solid 1px #e4e4e4;
          &:first-child {
            border-top: solid 1px #e4e4e4;
          }


          &.is-current {
             a {
               background: #f8f8f8;
               color: #2369ba;
             }
             svg {
              fill: #2369ba !important;
            }
           }



          & > a {
            align-items: center;
            display: flex;

            span {
              align-items: center;
              display: flex;
              height: 20px;
              justify-content: center;
              width: 20px;
            }

            p {
              margin-left: 8px;
              @include notMacosIos{
                margin-left: 5px;
              }
            }

            svg {
              &.categoryIcon {
                width: 19px;
              }
            }
          }
        }
      }
    }


}
