@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-pagination {
  align-content: center;
  display: flex;
  justify-content: center;
  margin-top: 100px;

  // 

  &--abb {
    align-items: center;
    display: flex;
    margin: 0 10px;
    width: 31px;

    svg {
      width: 100%;
    }
  }

  //  / 

  &--prev,
  &--next {
    align-items: center;
    display: flex;
    width: 6px;

    svg {
      transition: fill $comMS $comES;
      width: 100%;
    }

    &:hover {
      @include media(lg) {

        svg {
          fill: #4aa2db !important;
        }
      }
    }
  }

  &--prev {
    margin-right: 5px;
  }

  &--next {
    margin-left: 5px;
  }

  // 

  &--page {
    align-items: center;
    background-color: #f8f8f8;
    border: solid 1px #e4e4e4;
    border-radius: 3px;
    display: flex;
    height: 40px;
    justify-content: center;
    margin: 0 10px;
    transition: border-color $comMS $comES, color $comMS $comES;
    width: 30px;

    @include fz(14, 19, 400);

    &:hover {
      @include media(lg) {
        border-color: #4aa2db;
        color: #4aa2db;
      }
    }

    &.is-current {
      background-color: #4aa2db;
      border-color: #4aa2db;
      color: #fff;

      &:hover {
        color: #fff;
      }
    }
  }

  @include media(sm) {
    margin-top: vwset(60);

    // 

    &--abb {
      margin: 0 vwset(10);
      min-width: vwset(20);
      width: vwset(20);
    }

    //  / 

    &--prev,
    &--next {
      width: vwset(8);
    }

    &--prev {
      margin-right: vwset(10);
    }

    &--next {
      margin-left: vwset(10);
    }

    // 

    &--page {
      @include fz(13);

      border-radius: vwset(3);
      height: vwset(40);
      margin: 0 vwset(5);
      width: vwset(30);
    }
  }
}
