@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.font-format {

  .l-container {
    display: block;
    margin-top: 50px;
    @include media(sm) {
      margin-top: vwset(40);
      padding: 0 20px;
    }
  }

  &__area {
    &:first-of-type{
      margin-top: 0;
    }

    margin-top: 100px;
     width: 100%;
    &__title {
      font-size: 3rem;
      @include media(sm) {
        font-size: 2rem;
      }
    }
  }

  &__block{
    margin-top: 60px;
    display: block;
    border-bottom: 1px solid #ccc;
  }

  &__text{
    font-size: 1.2rem;

    &--red{
      font-size: 1.2rem;
      color: red;
    }
  }

  // 
  &__status{
    font-size: 1.1rem;
    @include media(sm){
      font-size: 1.0rem;
    }
    &--red{
      font-size: 1.1rem;
      color: red;
      @include media(sm){
        font-size: 1.0rem;
      }
    }
  }

  .c-textlink--small{
    margin-top: 20px;
  }
}
