@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.order-payment {

  .full {
    .l-form__list {
      dt {
        @include media(sm){
          min-width: auto;
        }
      }
    } 
  }

  .c-text--caution p {
    margin-top: 0;
  }

  .delivery-note{
    .l-form__list dt,
    .l-form__list dd{
      padding: 35px 27px;
      @include media(sm){
        padding: vwset(17) vwset(16);
      }
    }
  }
}
