@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;

.c-history-chat {
        margin-left: 60px;
        padding-bottom: 40px;
        position: relative;
        &.contact-title {
            margin-top: 30px;
        }
        &:first-of-type {
            .c-history-chat__inner {
                background-color: #F8F8F8;
            }
            .post-info {
                padding: 0 30px 19px;
                @include media(sm){
                    display: block;
                    padding-top: vwset(0);
                    padding-bottom: vwset(10);
                    padding-right: vwset(17);
                    padding-left: vwset(17);

                }
                @include media(se){
                    padding-top: vwset(0);
                    padding-bottom: vwset(10);
                    padding-left: vwset(17);
                    padding-right: vwset(17);
                }
            }
        }
        &:last-of-type {
            padding-bottom: 0;
        }
        &__inner {
            border: 1px solid #e4e4e4;
            border-radius: 5px;
            &:before {
                position: absolute;
                content: '';
                top: 0;
                left: -37px;
                width: 2px;
                height: 100%;
                padding-bottom: 30px;
                background-color: #E4E4E4;
            }
            &--last::before {
                width: 0px;
            }
        }
        &.question {
            &:after {
                content: url('data:image/svg+xml;utf-8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="50" height="50" viewBox="0 0 50 50"><defs><clipPath id="a"><circle cx="25" cy="25" r="25" fill="#5d5d5d"/></clipPath></defs><g transform="translate(-343 -672)"><circle cx="25" cy="25" r="25" transform="translate(343 672)" fill="#4aa2db"/><g transform="translate(343 672)" clip-path="url(#a)"><path d="M28.731,30.731A11.365,11.365,0,1,0,17.365,19.365,11.362,11.362,0,0,0,28.731,30.731Zm0,3.465C21.144,34.2,6,38,6,45.561v5.683H51.462V45.561C51.462,38,36.317,34.2,28.731,34.2Z" transform="translate(-3.731 -1.244)" fill="#fff"/></g><g transform="translate(343 672)" fill="none" stroke="#4aa2db" stroke-width="1"><circle cx="25" cy="25" r="25" stroke="none"/><circle cx="25" cy="25" r="24.5" fill="none"/></g></g></svg>');
                position: absolute;
                top: 0;
                left: -61px;
                width: 50px;
                height: 50px;
            }
        }
        &.answer {
            &:after {
                content: url('data:image/svg+xml;utf-8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="50" height="50" viewBox="0 0 50 50"><defs><clipPath id="a"><circle cx="25" cy="25" r="25" transform="translate(103 644)" fill="#5d5d5d"/></clipPath></defs><g transform="translate(-343 -857)"><circle cx="25" cy="25" r="25" transform="translate(343 857)" fill="#e07409"/><g transform="translate(240 213)" clip-path="url(#a)"><g clip-path="url(#a)"><path d="M48.847,47.749c-.832-3.674-5.581-5.465-7.222-6.043a53.842,53.842,0,0,0-6.02-1.155,6.117,6.117,0,0,1-2.785-1.283c-.462-.555-.185-5.7-.185-5.7a17.138,17.138,0,0,0,1.317-2.507,32.342,32.342,0,0,0,.971-4.379s.947,0,1.283-1.664c.358-1.814.924-2.519.855-3.871-.069-1.329-.8-1.294-.8-1.294a20.259,20.259,0,0,0,.786-5.928C37.153,9.18,33.433,4.5,26.7,4.5c-6.829,0-10.469,4.68-10.365,9.429a21.131,21.131,0,0,0,.774,5.928s-.728-.035-.8,1.294c-.069,1.352.5,2.057.855,3.871.324,1.664,1.283,1.664,1.283,1.664a32.342,32.342,0,0,0,.971,4.379,17.139,17.139,0,0,0,1.317,2.507s.277,5.142-.185,5.7a6.117,6.117,0,0,1-2.785,1.283,53.842,53.842,0,0,0-6.02,1.155c-1.641.578-6.39,2.369-7.222,6.043a.924.924,0,0,0,.913,1.121h42.51A.922.922,0,0,0,48.847,47.749Z" transform="translate(101.315 645.13)" fill="#fff"/></g></g><g transform="translate(343 857)" fill="none" stroke="#df7408" stroke-width="1"><circle cx="25" cy="25" r="25" stroke="none"/><circle cx="25" cy="25" r="24.5" fill="none"/></g></g></svg>');
                position: absolute;
                top: 0;
                left: -61px;
                width: 50px;
                height: 50px;
            }
        }
    
        .post-title {
            color: #60A1D6;
            font-size: 1.8rem;
            position: relative;
            margin: 10px 0 0 40px;
            @include media(sm){
                margin-top: vwset(10);
                margin-bottom: vwset(0);
                margin-left: vwset(28);
                margin-right: vwset(0);
            }
            &:before {
                position: absolute;
                content: '';
                position: absolute;
                display: inline-block;
                top: 50%;
                left: -10px;
                transform: translateY(-50%);
                width: 2px;
                height: 20px;
                background-color: #60A1D6;
            }
        }
        .post-info {
            padding: 17px 30px;
            display: flex;
            justify-content: space-between;
            @include media(sm){
                display: block;
                padding-top: vwset(17);
                padding-bottom: vwset(10);
                padding-left: vwset(17);
                padding-right: vwset(17);
            }
            @include media(se){
                padding-top: vwset(17);
                padding-bottom: vwset(10);
                padding-left: vwset(10);
                padding-right: vwset(10);
            }
            .post-time {
                position: relative;
                @include media(sm){
                    display: block;
                    padding-left: vwset(25);
                }
                &:after {
                    content: url('data:image/svg+xml;utf-8,<svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13"><g transform="translate(-2.5 -2.5)"><path d="M15,9A6,6,0,1,1,9,3a6,6,0,0,1,6,6Z" transform="translate(0 0)" fill="none" stroke="#393939" stroke-linecap="round" stroke-linejoin="round" stroke-width="1"/><path d="M18,9v3.6l2.4,1.2" transform="translate(-9 -3.6)" fill="none" stroke="#393939" stroke-linecap="round" stroke-linejoin="round" stroke-width="1"/></g></svg>');
                    position: absolute;
                    top: 2px;
                    left: -20px;
                    width: 12px;
                    height: 12px;
                    @include media(sm){
                        left: 2px;
                        width: vwset(12);
                        height: vwset(12);
                    }
                }
            }
        }
        .post-txt {
            padding: 17px 30px;
            border-top: 1px solid #e4e4e4;
            @include fz(14, 24);
            @include letterSpacing(50);
            @include media(sm){
                padding: 17px;
            }
        }
        
}

