@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.inquiry-detail {

    .c-history-item {
        height: 281px;
    }
    .c-text-s {
        @include letterSpacing(50);
    }
    .customer-input {
        width: 960px;
        height: 331px;
        background-color: #F8F8F8;
        border-radius: 6px;
        padding: 30px 60px;
        @include media(sm){
            width: 100%;
            height: auto;
            padding-top: vwset(24);
            padding-bottom: vwset(24);
            padding-left: vwset(20);
            padding-right: vwset(20);
        }
        h4 {
            @include fz(16, 27);
            @include letterSpacing(50);
        }
        textarea {
            background-color: #fff;
            margin: 15px 0 0 0;
        }
        .btn-2col {
            display: flex;
            justify-content: center;
            margin-top: 20px;
            @include media(sm){
                margin-top: 12px;
                flex-direction: column-reverse;
            }
            .c-btn--simple {
                margin-right: 30px;
                width: 280px;
                height: 45px;
                @include media(sm){
                    width: 100%;
                    margin-right: 0;
                    margin-top: vwset(20);
                }
            }
            .c-btn--blue {
                height: 45px;
                @include media(sm){
                    width: 100%;
                }
            }
        }
    }
}