@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/icon" as *;

// form
// ----------------------------------------------------------

.c-form {
  // margin-top: 30px;
  .c-title--line {
    margin-top: 40px;
  }
  &--wide {
    width: 370px;
  }
  &__text {
    margin-top: 20px;
  }
  &__sup {
    @include fz(12, 16, 500);
    margin-top: 10px;
  }
  &__intro {
    @include fz(12, 16, 500);
    margin-bottom: 20px;
  }
  table {
    width: 100%;
    th, td {
      text-align: left;
      padding: 20px;
      border-top: solid 1px #e4e4e4;
      border-bottom: solid 1px #e4e4e4;
    }
    th {
      background-color: #f8f8f8;
      font-weight: 500;
      & > div {
        display: flex;
        align-items: center;
        span {
          display: flex;
          justify-content: center;
          align-items: center;
          color: #fff;
          width: 50px;
          height: 23px;
          border-radius: 2px;
          margin-right: 10px;
          &.required {
            background-color: #E31616;
          }
          &.any {
            background-color: #A0A0A0;
          }
        }
      }
      & > .note {
        span {
          width: auto;
          display: inline-block;
          @include fz(13, 16, 500);
          color: #363636;
        }
      }
    }
    td {
      & > div {
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center;
        &:nth-of-type(n + 2) {
          margin-left: 25px;
        }
      }
    }
  }
  .c-input {
    &.name {
      width: 150px;
      margin-left: 10px;
    }
    &[type="email"] {
      width: 600px;
    }
    &[type="tel"] {
      width: 190px;
    }
    &[type="password"] {
      width: 400px;
    }
    &.zip01 {
      width: 70px;
      & + span {
        margin: 0 10px;
      }
    }
    &.zip02 {
      width: 90px;
      & + .c-btn--blueborder {
        margin: 0 20px;
      }
    }
    &.address {
      width: 600px;
    }
  }
  .c-selectbox {
    &.year {
      width: 100px;
      & + span {
        margin: 0 10px;
      }
    }
    &.month {
      width: 70px;
      & + span {
        margin: 0 10px;
      }
    }
    &.day {
      width: 70px;
      & + span {
        margin: 0 10px;
      }
    }
    &.address {
      width: 190px;
    }
  }


  .c-radio {
    & + label {
      font-weight: 500;
    }
  }
  .c-checkbox {
    p {
      font-weight: 500;
    }
  }



  nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    .c-btn--simple, .c-btn--blue {
      @include fz(16, 21, 600);
      width: 280px;
      height: 45px;
      margin: 0 15px;
    }
  }

  @include media(sm) {
    margin-top: vwset(30);
    &--wide {
      width: auto;
    }
    &__text {
      margin-top: vwset(20);
    }
    table {
      th, td {
        display: block;
        padding: vwset(15) vwset(10);
      }
      th {
        border-bottom: none;
        div {
          span {
            @include fz(12);
            width: vwset(50);
            height: vwset(23);
            border-radius: vwset(2);
            margin-right: vwset(10);
          }
        }
      }
      td {
        & > div {
          &:nth-of-type(n + 2) {
            margin-left: vwset(20);
          }
        }
      }
    }
    .c-input {
      &.name {
        width: vwset(100);
        margin-left: vwset(10);
      }
      &[type="email"] {
        width: 100%;
      }
      &[type="tel"] {
        width: 100%;
      }
      &[type="password"] {
        width: 100%;
      }
      &.zip01 {
        width: vwset(70);
        & + span {
          margin: 0 vwset(10);
        }
      }
      &.zip02 {
        width: vwset(90);
        & + .c-btn--blueborder {
          margin: 0 vwset(10);
        }
      }
      &.address {
        width: 100%;
      }
    }
    .c-selectbox {
      &.year {
        width: vwset(200);
        & + span {
          margin: 0 vwset(10);
        }
      }
      &.month {
        width: vwset(100);
        margin-top: vwset(10);
        & + span {
          margin: 0 vwset(10);
        }
      }
      &.day {
        width: vwset(100);
        margin-top: vwset(10);
        & + span {
          margin: 0 vwset(10);
        }
      }
      &.address {
        width: vwset(200);
      }
    }

    nav {
      flex-wrap: wrap;
      margin-top: vwset(30);
      .c-btn--simple, .c-btn--blue {
        @include fz(14);
        width: 100%;
        height: vwset(45);
        margin: vwset(15) 0 0;
        &:first-child {
          @include media(sm) {
            margin: 0;
          }
        }
      }
    }
  }
}



// input
// ----------------------------------------------------------

.c-input {
  @include fz(13, 20);

  border: solid 1px #707070;
  border-radius: 3px;
  height: 40px;
  outline: none;
  padding: 0 10px;
  transition: border-color $comMS $comES;
  background-color: #fff;

  &:hover,
  &:focus {
    @include media(lg) {
      border-color: #4aa2db;
    }
  }

  &::placeholder {
    color: #a0a0a0;
  }

  &::-ms-input-placeholder {
    color: #a0a0a0;
  }

  &:-ms-input-placeholder {
    color: #a0a0a0;
  }

  @include media(sm) {
    @include fz(16, 23);

    height: vwset(45);
    padding: 0 vwset(12);
  }
}

// textarea
// ----------------------------------------------------------

.c-text-area {
  border: 1px solid #707070;
  border-radius: 4px;
  box-sizing: border-box;
  height: 150px;
  margin: 5px 0;
  padding: 20px;
  resize: none;
  width: 100%;
  background-color: #fff;
  @include fz(13, 20);

  &::placeholder {
    color: #a0a0a0;
  }

  &::-ms-input-placeholder {
    color: #a0a0a0;
  }

  &:-ms-input-placeholder {
    color: #a0a0a0;
  }

  &:focus {
    outline: none;
  }


  @include media(sm) {
    @include fz(16, 23);
  }
}

// radio
// ---------------------------------------------------------

.c-radio {
  display: none;
}

.c-radio + label {
  cursor: pointer;
  padding-left: 25px;
  position: relative;
  font-weight: 600;

  &:hover{
    &:before{
      border: 2px solid #60a1d6;
      transition: all .3s;
    }
  }

  &::before {
    border: 2px solid #a0a0a0;
    border-radius: 50%;
    bottom: 0;
    content: "";
    display: block;
    height: 18px;
    left: 0;
    margin: auto;
    position: absolute;
    top: 0;
    transition: all .3s;
    width: 18px;
  }

  &::after {
    background: transparent;
    border-radius: 50%;
    bottom: 0;
    content: "";
    display: block;
    height: 10px;
    left: 4px;
    margin: auto;
    position: absolute;
    top: 0;
    transition: all .3s;
    width: 10px;
  }
}

.c-radio:disabled + label {
  color: #a0a0a0;

  &::before {
    border: 2px solid #a0a0a0;
    opacity: .4;
    transition-delay: 0s;
    transition-duration: 0s;
  }
}
.c-radio + label + .c-selectbox-form__wrap + label {
  @include media(sm){
    margin-left: vwset(15);
  }
}

.c-radio:disabled + label + .c-selectbox-form__wrap + label {
  color: #a0a0a0;
  @include media(sm){
    margin-left: vwset(15);
  }
}


.c-radio:checked + label {

  &::before {
    border: 2px solid #60a1d6;
  }

  &::after {
    background: #60a1d6;
    border-radius: 50%;
    bottom: 0;
    content: "";
    display: block;
    height: 10px;
    left: 4px;
    margin: auto;
    position: absolute;
    top: 0;
    width: 10px;
  }
}

// checkbox
// ----------------------------------------------------------

.c-checkbox {
  align-items: center;
  cursor: pointer;
  display: inline-flex;
  @include fz(13, 20);

  @include media(sm) {
    align-items: flex-start;
    @include fz(16, 23);
  }

  &:hover {

    .c-checkbox__box {

      span {
        border-color: #60a1d6;
      }
    }
  }

  &__box {

    input[type = "checkbox"] {
      display: none;
    }

    span {
      background-color: #fff;
      background-position: center;
      background-size: 13px auto;
      border: solid 2px #a0a0a0;
      border-radius: 3px;
      display: block;
      height: 18px;
      transition: border-color $comMS $comES, background-color $comMS $comES;
      width: 18px;

      @include icon('check', 'ffffff');
    }

    input[type = "checkbox"]:disabled + span {
      opacity: .4;
    }

    input[type = "checkbox"]:disabled + label p{
      color: #a0a0a0;
    }

    input[type = "checkbox"]:focus:disabled + span,
    input[type = "checkbox"]:hover:disabled + span {
      border: solid 2px #a0a0a0;
      opacity: .4;
    }


    input[type = "checkbox"]:checked + span {
      background-color: #60a1d6;
      border-color: #60a1d6;
    }
  }

  p {
    @include fz(14, 19, 600);

    margin-left: 5px;
  }

  &:disabled{
    p{
      color: #a0a0a0;
    }

  }
}

// c-checkbox--disabled
// ----------------------------------------------------------

.c-checkbox--disabled{
  display: none;
}
.c-checkbox--disabled + label {
  cursor: pointer;
  padding-left: 25px;
  position: relative;
  font-weight: 600;

  &:hover {
    &:before{
      border-color: #60a1d6;
    }
  }

  &::before{
    content: "";
    width: 18px;
    height: 18px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    margin: auto;
    background-color: #fff;
    background-position: center;
    background-size: 13px auto;
    border: solid 2px #a0a0a0;
    border-radius: 3px;
    transition: border-color $comMS $comES, background-color $comMS $comES;

  }

  &::after{
    content: "";
    opacity: 0;
    width: 13px;
    height: 13px;
    @include icon('check', 'ffffff');
    position: absolute;
    border-radius: 2px;
    top: 4px;
    left: 3px;
    transition: border-color $comMS $comES, background-color $comMS $comES;

  }

}

.c-checkbox--disabled:checked + label {
  &::before{
    content: "";
    border-color: #60a1d6;
    background-color: #60a1d6;
  }
  &::after{
    content: "";
    opacity: 1;

    border-color: #60a1d6;
  }

}

.c-checkbox--disabled:disabled + label {
  color: #a0a0a0;
  &::before{
    opacity: .4;
  }
  &:hover {
    &::before{
      border-color: #a0a0a0;
    }
  }
}

