// formselectbox
// ----------------------------------------------------------

.c-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #fff;
  border: 1px solid #707070;
  border-radius: 3px;
  color: inherit;
  color: #393939;
  cursor: pointer;
  margin-right: 3px;
  outline: none;
  padding: 9px 40px 9px 10px;
  position: relative;
  text-indent: .01px;
  vertical-align: middle;
  width: 190px;
  @include fz(13, 20);

  @include media(sm) {
    display: block;
    width: 100%;
    padding: vwset(14) vwset(40) vwset(14) vwset(14);
    @include fz(16, 23);
  }

  &--small{
    width: 90px;
    //
    &-birth {
    width: 90px;
      @include media(sm) {
        width: vwset(75) !important;
        padding: vwset(14) vwset(15) vwset(14) vwset(8);
      }
    }
  }

  //
  &--pref {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: #fff;
    border: 1px solid #707070;
    border-radius: 3px;
    color: inherit;
    color: #393939;
    cursor: pointer;
    margin-right: 3px;
    outline: none;
    padding: 9px 7px 9px 10px;
    position: relative;
    text-indent: .01px;
    vertical-align: middle;
    width: 190px;
    @include fz(13, 20);

    @include media(sm) {
      display: block;
      padding: vwset(14) vwset(40) vwset(14) vwset(14);
      @include fz(16, 23);
    }
  }

  &--gray{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    height: 35px;
    padding: 7px 24px 7px 10px;
    border: 1px solid #e4e4e4;
    background-color: #f8f8f8;
    border-radius: 3px;
    color: inherit;
    color: #393939;
    cursor: pointer;
    outline: none;
    position: relative;
    text-indent: .01px;
    vertical-align: middle;
    @include fz(12, 18);

  }

  option {
    background-color: #fff;
    color: #333;
  }

  &:disabled {
    opacity: .4;

    &:focus {
      opacity: .4;
    }
  }

  &__wrap {
    display: inline-block;
    position: relative;

    &:before {
      border-bottom: 2px solid #525151;
      border-right: 2px solid #525151;
      content: '';
      display: inline-block;
      height: 9px;
      position: absolute;
      right: 16px;
      top: 48%;
      transform: translateY(-50%) rotate(45deg);
      width: 9px;
      z-index: 1;
    }

    &--gray{
      display: inline-block;
      position: relative;

      &:nth-of-type(n+2){
        margin-left: 10px;
      }

      &:before {
        border-bottom: 2px solid #a0a0a0;
        border-right: 2px solid #a0a0a0;
        content: '';
        display: inline-block;
        height: 6px;
        position: absolute;
        right: 10px;
        top: 48%;
        transform: translateY(-50%) rotate(45deg);
        width: 6px;
        z-index: 1;
      }

    }

  }
}


// 
// ----------------------------------------------------------

.c-require {
  background-color: #e31616;
  border-radius: 2px;
  color: #fff;
  line-height: 2;
  margin-right: 10px;
  padding: 4px 10px 4px 13px;
  display: inline-block;
  @include fz(12, 18, 600);
  @include letterSpacing(300);
  white-space: nowrap;
}

.c-not-require {
  background-color: #a0a0a0;
  border-radius: 2px;
  color: #fff;
  line-height: 2;
  margin-right: 10px;
  padding: 4px 10px 4px 13px;
  display: inline-block;
  @include fz(12, 18, 600);
  @include letterSpacing(300);
  white-space: nowrap;
}

.c-expiration{
  display: flex;
  align-items: center;
  .c-selectbox{
    width: 74px;
    @include media(sm){
      width: 20vw;
    }
  }

  &__separation{
    margin: 0 10px;
  }
}
