@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.guide {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include media(sm){
      display: block;
    }
    .item {
      width: calc(50% - 20px);
      list-style: none;
      @include media(sm) {
        width: auto;
      }

      &:nth-child(2) {
        @include media(sm) {
          margin-top: vwset(32);
        }
      }

      &:nth-child(n+3) {
        margin-top: 40px;
        @include media(sm) {
          margin-top: vwset(32);
        }
      }

      &__link {
        background-color: map-get($color, whitebg);
        padding: 16px 20px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        transition: background-color .4s ease;

        &::after {
          @include arrow(6, 2, map-get($color, gray), right);

          margin-right: 0;
          margin-left: auto;
          flex: none;
          transition: opacity .4s ease;
        }

        svg {
          width: 22px;
          height: 22px;
          margin-right: 12px;
          transition: opacity .4s ease;
        }

        &:hover {
          @include media(lg) {
            background-color: map-get($color, linegray);
          }
        }
      }

      &__children {
        margin-left: 20px;
        margin-right: 20px;
        padding-top: 10px;
        list-style: none;
        @include media(sm) {
          margin-left: vwset(16);
          margin-right: vwset(16);
          padding-top: vwset(8);
        }

        &__item {
          margin-top: 10px;
          @include media(sm) {
            margin-top: vwset(8);
          }

          &__link {
            display: flex;
            align-items: center;
            transition: color .4s ease;

            &::after {
              @include arrow(6, 2, map-get($color, gray), right);
              margin-left: 10px;
            }

            &:hover {
              color: map-get($color, blue);

              &::after {
                border-color: map-get($color, blue);
              }
            }
          }
        }
      }
    }
  }
}
