
@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.c-confirm-list {
  // margin-top: 10px;
  //   @include media(sm) {
  //     margin-top: vwset(10);
  //   }

    // &:nth-of-type(n + 2) {
    //   margin-top: 40px;
    // }

  dl {
    border-bottom: 1px solid #e4e4e4;
    display: flex;
    font-size: 1.4rem;

    @include media(sm) {
      flex-direction: column;
    }
  }
  // .c-title--section + dl {
  //   margin-top: 10px;
  // }

  dl:first-of-type {
    border-top: 1px solid #e4e4e4;
  }

  &__title {
    align-items: center;
    background: #f8f8f8;
    display: flex;
    padding: 17px 20px;
    width: 340px;
    .full & {
      min-width: 320px;
      @include media(sm) {
        min-width: 100%;
      }
    }

    @include media(sm) {
      width: 100%;
    }
  }

  &__detail {
    align-items: center;
    display: flex;
    min-height: 60px;
    padding: 17px 27px;
    width: 830px;
    word-break: break-all;

    @include media(sm) {
      min-height: 70px;
      padding: 17px 20px;
      width: 100%;
    }

    a {
      &:not([href^="tel:"]){
        text-decoration: underline;

        &:hover {
          opacity: .5;
        }
      }
    }
  }

  &__deliver {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
  }

  &__text-wrap {
    display: flex;
    flex-direction: column;
    padding: 17px 27px;

    > dd {
      padding: 0;
    }

    @include media(sm) {
      padding: 17px 20px;
    }
  }

  &__sub-group {

    > p:first-child {
      margin-right: 45px;
    }

    > p {
      display: inline-block;
      padding: 0;
    }
  }
}
