@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.login-inquiry {

  .c-title--page {
    justify-content: center;
    & + .c-text--xs {
      text-align: center;
      // margin-top: 35px;
    }

    // @include media(sm) {
    //   & + .c-text--xs {
    //     margin-top: vwset(20);
    //   }
    // }
  }

  &__area {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 37px 40px 38px;
    margin: 0 auto;
    width: 560px;
    nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 38px;
      a {
        margin-top: 7px;
      }
    }

    @include media(sm) {
      border-radius: 5px;
      padding: vwset(24);
      margin: vwset(36) auto 0;
      width: auto;
      nav {
        margin-top: vwset(24);
      }
    }
  }

  &__list {
    &:nth-of-type(n + 2) {
      margin-top: 18px;
    }
    dd {
      margin-top: 10px;
      input {
        width: 100%;
      }
    }

    .tel-txt {
      span {
        display: block;
        margin-top: 7px;
      }
    }

    @include media(sm) {
      &:nth-of-type(n + 2) {
        margin-top: 18px;
      }
      dd {
        margin-top: 10px;
      }

      .tel-txt {
        span {
          margin-top: 7px;
        }
      }
    }
  }

  &__textlink {
    display: flex;
    justify-content: center;
  }
}
