@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;
@use "../Foundation/icon" as *;

.l-nav {
  // PC

  &-lg {
    display: flex;
    margin-left: 30px;

    span {
      align-items: center;
      display: flex;
      justify-content: center;
      transition: opacity $comMS $comES;
    }

    a {
      align-items: center;
      display: flex;
      position: relative;

      em {
        align-items: center;
        background-color: #ed6161;
        border-radius: 50%;
        color: #fff;
        display: flex;
        font-style: normal;
        height: 16px;
        justify-content: center;
        position: absolute;
        right: 10px;
        text-align: center;
        top: 21px;
        width: 16px;

        @include fz(10, 14, 600);
      }
    }

    & > div {
      align-items: center;
      display: flex;
    }

    & > a,
    & > div {
      flex-direction: column;
      justify-content: center;
      position: relative;
      width: 62px;

      &:hover {

        & > span {
          opacity: .5;
        }
      }

      & > span {
        height: 20px;
        margin-top: 4px;
        width: 30px;
      }

      & > p {
        color: #a0a0a0;
        height: 14px;
        margin-top: 4px;
        text-align: center;
        transition: opacity $comMS $comES, visibility $comMS $comES, height $comMS $comES, margin-top $comMS $comES;

        @include fz(10, 14, 600);
        @include letterSpacing(75);
      }

      & > span {

        svg {

          &.cart {
            width: 22px;
          }

          &.login {
            width: 19px;
          }

          &.favorite {
            width: 19px;
          }
        }
      }
    }

    // 

    .is-scroll & {

      & > a,
      & > div {

        & > p {
          height: 0;
          margin-top: 0;
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    // PC

    &__submenu {
      background-color: #f8f8f8;
      border: solid 1px #e4e4e4;
      border-radius: 4px;
      left: 50%;
      opacity: 0;
      position: absolute;
      top: calc(100% - 2px);
      transform: translateX(-50%);
      transition: opacity $comMS $comES, visibility $comMS $comES;
      visibility: hidden;
      width: 170px;

      &:before,
      &:after {
        border-style: solid;
        content: '';
        height: 0;
        left: 50%;
        position: absolute;
        transform: translateX(-50%);
        width: 0;
      }

      &:before {
        border-color: transparent transparent #e4e4e4 transparent;
        border-width: 0 4.5px 6px 4.5px;
        top: -6px;
      }

      &:after {
        border-color: transparent transparent #f8f8f8 transparent;
        border-width: 0 3.5px 5px 3.5px;
        top: -5px;
      }

      & > a {
        padding: 13px 20px;
        transition: opacity $comMS $comES;

        &:hover {
          opacity: .5;
        }

        &:nth-of-type(n + 2) {
          border-top: solid 1px #e4e4e4;
        }

        & > span {
          height: 15px;
          width: 15px;
        }

        & > p {
          @include fz(13, 17, 600);
          @include letterSpacing(75);

          margin-left: 10px;
        }

        & > span {

          svg {

            &.login {
              width: 11px;
            }

            &.regist {
              width: 11px;
            }
          }
        }
      }

      .js-hover-btn:hover & {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  // 

  &-sm {
    align-items: center;
    display: flex;
    margin-right: vwset(11);
    order: 3;

    &__btn {
      align-items: center;
      background-position: center;
      display: flex;
      height: vwset(40);
      justify-content: center;
      position: relative;
      width: vwset(40);

      em {
        align-items: center;
        background-color: #ed6161;
        border-radius: 50%;
        color: #fff;
        display: flex;
        font-style: normal;
        height: vwset(16);
        justify-content: center;
        position: absolute;
        right: 0;
        text-align: center;
        top: 0;
        width: vwset(16);

        @include fz(10, 14, 600);
      }

      span {

        svg {

          &.search {
            width: vwset(20);
          }

          &.cart {
            width: vwset(23);
          }
        }
      }
    }
  }

  // 

  &-slide {
    background-color: #fff;
    height: 100%;
    left: -100%;
    overflow-y: auto;
    padding-bottom: vwset(20);
    position: fixed;
    top: 0;
    transition: left $comMS $comES;
    width: calc(100% - #{vwset(60)});
    z-index: 900;

    .is-nav-active & {
      left: 0;
    }

    &__header {
      background-color: #e4e4e4;
      padding: vwset(20);

      a {
        align-items: center;
        background-color: #fff;
        background-position: right vwset(15) center;
        background-size: vwset(7) auto;
        display: flex;
        padding: vwset(20);
        transition: opacity $comMS $comES;

        @include icon('arrow-right', 'a0a0a0');

        &:nth-of-type(n + 2) {
          margin-top: vwset(10);
        }

        p {
          @include fz(14, 21, 600);
        }

        svg {

          &.login {
            margin-right: vwset(16);
            width: vwset(14);
          }

          &.regist {
            margin-right: vwset(16);
            width: vwset(14);
          }

          &.favorite {
            margin-right: vwset(13);
            width: vwset(17);
          }
        }
      }
    }

    &__body {

      &--title {
        background-color: #f8f8f8;
        display: block;
        padding: vwset(20);

        @include fz(14, 21, 600);
      }

      ul {
        list-style: none;

        li {

          &[data-hierarchy = "1"] {
            border-bottom: solid 1px #e4e4e4;

            & > .l-nav-slide--btn {

              a {
                padding: vwset(20) 0 vwset(20) vwset(20);

                p {
                  @include fz(16, 21, 600);
                }

                svg {

                  &.categoryIcon {
                    height: vwset(19.06);
                    margin-right: vwset(10);
                    width: vwset(20);
                  }

                }
              }

              span {
                //height: vwset(60);
                width: vwset(60);

                &:before,
                &:after {
                  background-color: #4aa2db;
                  width: vwset(16);
                }
              }
            }

            & > .js-accordion-block {
              margin: 0 vwset(20);
            }
          }

          &[data-hierarchy = "2"] {

            &:nth-of-type(n + 2) {
              margin-top: vwset(5);
            }

            &:last-of-type {
              padding-bottom: vwset(20);
            }

            & > .l-nav-slide--btn {
              background-color: #f8f8f8;
              border-radius: vwset(3);
              height: vwset(50);

              a {
                padding: 0 0 0 vwset(29);

                p {
                  @include fz(14, 19, 400);
                }
              }

              span {
                height: vwset(50);
                width: vwset(50);

                &:before,
                &:after {
                  background-color: #a0a0a0;
                  width: vwset(12);
                }
              }
            }
          }

          &[data-hierarchy = "3"] {
            background-color: #f8f8f8;
            border-top: solid 1px #e4e4e4;

            a {
              align-items: center;
              display: flex;
              height: vwset(50);
              padding: 0 0 0 vwset(29);

              svg {
                width: vwset(6);
              }

              p {
                @include fz(13, 18, 400);

                margin-left: vwset(7);
              }
            }
          }
        }
      }

      .js-accordion-block {
        height: 0;
        opacity: 0;
        overflow: hidden;
        transition: height $comMS $comES, opacity $comMS $comES, visibility $comMS $comES;
        visibility: hidden;

        &.is-opened {
          opacity: 1;
          visibility: visible;
        }
      }
    }

    &--btn {
      display: flex;

      &.is-opened {

        span {

          &:after {
            transform: translate(-50%, -50%) rotate(0deg);
          }
        }
      }

      a {
        display: flex;
        flex-grow: 1;
        padding: 0 0 0 vwset(20);
       align-items: center;

        p {
          align-items: center;
          display: flex;
          flex-grow: 1;

          @include fz(14, 21, 600);
        }

        svg {

          &.categoryIcon {
            margin-right: vwset(10);
            width: vwset(20);
          }

        }
      }

      span {
        //height: vwset(60);
        position: relative;
        width: vwset(60);

        &:before,
        &:after {
          border-radius: vwset(2);
          content: '';
          height: vwset(2);
          left: 50%;
          position: absolute;
          top: 50%;
          transform-origin: center;
          transition: transform $comMS $comES;
        }

        &:before {
          transform: translate(-50%, -50%);
        }

        &:after {
          transform: translate(-50%, -50%) rotate(270deg);
        }
      }
    }

    // 

    &__banner {
      margin: vwset(20) vwset(20) 0;

      a {
        display: block;

        img {
          width: 100%;
        }
      }
    }
  }
}
