@charset "utf-8";

@use "../Foundation/variables" as *;
@use "../Foundation/mixin" as *;
@use "../Foundation/functions" as *;

.guide-payment {

  .c-text--caution {
    padding: 25px 20px 35px;
  }

  .c-title--line {
    padding-top: 16px;

    @include media(sm) {
      padding-top: 8px;
    }
    &__sub {
      display: block;
    }
  }

  .column2 {
    display: flex;
    justify-content: space-between;

    @include media(sm) {
      flex-direction: column;
    }

    .item {
      width: calc(50% - 20px);

      @include media(sm) {
        width: 100%;
        text-align: center;
      }

      &__inner {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        >img {
          width:45%;
        }
        >img:nth-child(1) {
          margin-right: 10px;
        }
      }

      + .item {
        @include media(sm) {
          margin-top: 1em;
        }
      }

      .c-text--s {
        @include media(sm) {
          text-align: left;
        }
      }
    }
  }

  &__demo {
    &-inner {
      display: flex;
      @include media(sm) {
        display: flex;
      }
      >a:nth-child(1) {
        margin-right: 10px;
      }
      &:last-of-type {
        margin-top: 10px;
      }
    }
    &-inner-top {
      @include media(sm) {
        display: flex;
      }
    }
    &-image {
      @include media(sm) {
        width: 45%;
      }
      &:first-child {
        @include media(sm) {
          margin-right: 20px;
        }
      }
    }
    a {
      display: inline-block;
      margin-right: 20px;

      @include media(sm) {
        width: 100%;
      }
    }
    img {
      @include media(sm) {
        width: 100%;
      }
    }
  }

  .c-list--note {
    margin-top: 1em;
  }

  .c-list--dot {
    margin-top: 1em;
  }
}
